/**
 *
 * EditParserModal
 *
 */

import React from 'react';
import styled from 'styled-components';

import PlayIcon from 'react-icons/lib/fa/play';
import HideHelpIcon from 'react-icons/lib/fa/question-circle';
import DisplayHelpIcon from 'react-icons/lib/fa/question';

import { Modal, ModalHeader, ModalBody, ModalFooter, Button } from 'reactstrap';

import CbCodeEditor from 'components/CbCodeEditor/Loadable';
import { CbEditorLanguages, CbEditorTheme } from 'components/CbCodeEditor/';

import SplitPane from 'react-split-pane';

import ParserHelp from '../ParserHelp';
import CbParserModel from 'components/CbCodeEditor/editorModels/CbParserModel';
import CbButton from 'components/CbButton';

// Libraries to include in the monaco editor
// @ts-ignore
import jqueryDefs from '@types/jquery/index.d.ts';
// @ts-ignore
import paho from '@types/paho-mqtt/index.d.ts';
import cbDefs from 'clearblade-js-client/index.d.ts';
import { BaseHTMLModel } from 'plugins/widgets/HTMLWidget/def';
import styleVars from 'cssOverrides/styleVars';

// ts definition files to pass into the monaco editor
const tsDefs = [
  { name: 'jquery', contents: jqueryDefs },
  { name: 'paho', contents: paho },
  { name: 'clearbladejs', contents: cbDefs },
];

const parserModel = new CbParserModel();

const ParserModal = styled(Modal)`
  && {
    min-width: 49vw;
    margin-top: 0;
    margin-bottom: 0;
    margin-left: 1vw;
    box-shadow: 5px 0 5px -2px #888;
  }
`;

const EditParserContainer = styled.div`
  height: 100vh;
  pointer-events: auto;
  display: flex;
  flex-direction: column;
`;

const EditParserFooter = styled(ModalFooter)`
  bottom: 0;
  position: absolute;
  width: 48.8vw;
`;

const ParserModalHeader = styled(ModalHeader)`
  && {
    justify-content: flex-end;
  }
`;

const ParserModalBody = styled(ModalBody)`
  && {
    height: 80vh;
    /*remove padding so that the code editor can span the width of the screen*/
    padding-left: 0;
    padding-right: 0;
    display: flex;
    flex-direction: column;
  }
`;

const PanelContainer = styled.div`
  /* ideally this would be responsive */
  height: 10px;
  flex-grow: 1;
`;

const SplitPaneContainer = styled(SplitPane)`
  position: static !important;
`;

const ResizerStyles = {
  backgroundColor: styleVars.gray600,
  height: '3px',
  margin: '1px',
  cursor: 'row-resize',
  width: '100%',
  zIndex: 5,
};

type Parser = string | BaseHTMLModel;

interface IProps {
  split: boolean;
  onClose: (parser: Parser) => void;
  onTest: (parser: Parser) => void;
  onDismiss: () => void;
  title?: string;
  currentParserSourceType: string;
  currentParserType: string;
  parser: Parser;
  expectedFormatTips: string;
}

interface IState {
  parser: Parser;
  collapseHelp: boolean;
}

class EditParserModal extends React.Component<IProps, IState> {
  state: IState = {
    parser: this.props.parser,
    collapseHelp: true,
  };

  _handleUpdateEditorSize = () => {
    // force monaco editor to acknowledge height change made by react-split-pane
    window.dispatchEvent(new Event('resize'));
  };

  _handleCodeChange = (parser: string) => {
    this.setState({
      parser,
    });
  };

  _handleCodeChangeSplit = (e: string, x: string) => {
    // to split the monaco editor, this.state.parser becomes an object with the keys being the language for each instance
    this.setState({
      parser: { ...(this.state.parser as BaseHTMLModel), ...{ [e]: x } },
    });
  };

  _onSubmit = () => {
    this.props.onClose(this.state.parser);
  };

  _testParser = () => {
    this.props.onTest(this.state.parser);
  };

  _toggleHelp = () => {
    this.setState({ collapseHelp: !this.state.collapseHelp });
  };

  render() {
    const helpIcon = this.state.collapseHelp ? (
      <DisplayHelpIcon />
    ) : (
      <HideHelpIcon />
    );

    return (
      <ParserModal
        isOpen={true}
        toggle={this.props.onDismiss}
        keyboard={false} // Prevent the modal from being closed when the escape key is pressed
        backdrop={false}
        wrapClassName="modalWrapClass"
      >
        <EditParserContainer>
          <ParserModalHeader>
            {this.props.title}
            <CbButton
              color="success"
              onClick={this._testParser}
              title="Cmd + R"
            >
              <PlayIcon />
              &nbsp; Test
            </CbButton>
            &nbsp;
            <CbButton color="primary" onClick={this._toggleHelp}>
              {helpIcon}
            </CbButton>
          </ParserModalHeader>

          <ParserModalBody>
            <ParserHelp
              showCollapsed={this.state.collapseHelp}
              currentParserSourceType={this.props.currentParserSourceType}
              currentParserType={this.props.currentParserType}
              expectedFormatTips={this.props.expectedFormatTips}
            />

            <PanelContainer>
              {this.props.split ? (
                <SplitPaneContainer
                  split="horizontal"
                  defaultSize="66%"
                  allowResize
                  onDragFinished={this._handleUpdateEditorSize}
                  resizerStyle={ResizerStyles}
                  primary="second"
                >
                  <CbCodeEditor
                    width="100%"
                    height="95%"
                    language={CbEditorLanguages.HTML}
                    theme={CbEditorTheme.DARK}
                    value={(this.state.parser as BaseHTMLModel).HTML}
                    onChange={(value) =>
                      this._handleCodeChangeSplit('HTML', value)
                    }
                    onTestShortcut={this._testParser}
                    options={{
                      automaticLayout: true,
                    }}
                  />
                  <SplitPaneContainer
                    split="horizontal"
                    defaultSize="50%"
                    allowResize
                    onDragFinished={this._handleUpdateEditorSize}
                    resizerStyle={ResizerStyles}
                    primary="second"
                  >
                    <CbCodeEditor
                      width="100%"
                      height="95%"
                      language={CbEditorLanguages.CSS}
                      theme={CbEditorTheme.LIGHT}
                      value={(this.state.parser as BaseHTMLModel).CSS}
                      onChange={(value) =>
                        this._handleCodeChangeSplit('CSS', value)
                      }
                      onTestShortcut={this._testParser}
                      options={{
                        automaticLayout: true,
                      }}
                    />
                    <CbCodeEditor
                      cbModel={parserModel}
                      tsDefs={tsDefs}
                      width="100%"
                      height="80%"
                      language={CbEditorLanguages.JAVASCRIPT}
                      theme={CbEditorTheme.LIGHT}
                      value={(this.state.parser as BaseHTMLModel).JavaScript}
                      onChange={(value) =>
                        this._handleCodeChangeSplit('JavaScript', value)
                      }
                      onTestShortcut={this._testParser}
                      options={{
                        automaticLayout: true,
                      }}
                    />
                  </SplitPaneContainer>
                </SplitPaneContainer>
              ) : (
                <CbCodeEditor
                  cbModel={parserModel}
                  tsDefs={tsDefs}
                  width="95%"
                  height="90%"
                  language={CbEditorLanguages.JAVASCRIPT}
                  theme={CbEditorTheme.LIGHT}
                  value={this.state.parser as string}
                  onChange={(value) => this._handleCodeChange(value)}
                  onTestShortcut={this._testParser}
                  options={{
                    automaticLayout: true,
                  }}
                />
              )}
            </PanelContainer>
          </ParserModalBody>

          <EditParserFooter>
            <Button color="default" onClick={this.props.onDismiss}>
              Cancel
            </Button>
            <Button color="primary" onClick={this._onSubmit}>
              Apply
            </Button>
          </EditParserFooter>
        </EditParserContainer>
      </ParserModal>
    );
  }
}

export default EditParserModal;



