/**
 *
 * ImportPluginButton
 *
 */

import React from 'react';
import { UploadIcon } from '../Icons';
import { FormattedMessage } from 'react-intl';
import { connect } from 'react-redux';
import { CBDispatch } from 'cb-utils/typeUtils';
import {
  addSuccessNotification,
  addErrorNotification,
} from 'containers/Portal/actions';
import ImportExportHelp from '../ImportExportHelp';
// import styled from 'styled-components';

interface ImportPluginButtonProps {
  btnText: FormattedMessage.MessageDescriptor;
  shapeChecker: (plugin: any) => boolean;
  onComplete: (plugin: any) => void;
  successMsg: string;
  badShapeMsg: string;
  helpInfo?: FormattedMessage.MessageDescriptor;
}

export class ImportPluginButton extends React.Component<
  ImportPluginButtonProps & MapDispatchToProps,
  {}
> {
  fileInput: HTMLInputElement = null;
  render() {
    const { btnText, helpInfo } = this.props;
    return (
      <div>
        {helpInfo && <ImportExportHelp bodyText={helpInfo} />}
        <label className="btn btn-primary">
          <UploadIcon />
          &nbsp;
          <FormattedMessage {...btnText} />
          <input
            data-cy="import-plugin-input"
            type="file"
            hidden
            accept=".json"
            onChange={this.importPlugin}
            ref={(ref) => (this.fileInput = ref)}
          />
        </label>
      </div>
    );
  }

  importPlugin = (e: React.ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files[0];

    const r = new FileReader();
    r.onload = (theFile) => {
      try {
        const contents = JSON.parse(theFile.target.result);
        if (this.props.shapeChecker(contents)) {
          this.props.onComplete(contents);
          this.props.onImportSuccess(this.props.successMsg);
          this.fileInput.value = ''; // clear the value in case users try to import the same file
        } else {
          throw new Error(this.props.badShapeMsg);
        }
      } catch (err) {
        this.props.onImportError(err.message);
      }
    };
    r.readAsText(file);
  };
}

const mapDispatchToProps = (dispatch: CBDispatch) => ({
  onImportSuccess: (msg: string) => dispatch(addSuccessNotification(msg)),
  onImportError: (msg: string) => dispatch(addErrorNotification(msg)),
});

type MapDispatchToProps = ReturnType<typeof mapDispatchToProps>;

export default connect(null, mapDispatchToProps)(ImportPluginButton);



