import React from 'react';
import groupBy from 'lodash/groupBy';
import { Map } from './console-entity-models';

interface IGroupable {
  group?: string;
}

export const groupAndFlatten = (types: Map<IGroupable> | IGroupable[]) => {
  const groups = groupBy(types, (type) => (type.group ? type.group : 'Other'));
  const sortedGroups = Object.keys(groups).sort((a, b) => (a > b ? 1 : -1));
  return sortedGroups.reduce((acc, group) => {
    acc.push(
      {
        name: group,
        display_name: group,
        disabled: true,
      },
      ...groups[group],
    );
    return acc;
  }, []);
};

const groupOptions = (types: Map<IGroupable>) => {
  const flatSortedGroups = groupAndFlatten(types);
  return flatSortedGroups.map((val, i) => {
    if (val.disabled) {
      return (
        <option key={i} disabled>
          --- {val.display_name} ---
        </option>
      );
    } else {
      return (
        <option
          key={i}
          data-test="typeOption"
          value={val.type_name}
          title={val.description || null}
        >
          {val.display_name}
        </option>
      );
    }
  });
};

export default groupOptions;



