/**
 *
 * DatasourceEditor
 *
 */

import React from 'react';
import EditIcon from 'react-icons/lib/fa/edit';
import ParserButton from '../ParserButton';
import { Col, Row, Button } from 'reactstrap';
import CbInputField from 'components/CbInputField';
import {
  INCOMING_PARSER,
  OUTGOING_PARSER,
} from '../../containers/Portal/constants';
import {
  CBEvent,
  IDatasourceSuggestions,
  DataSettingDefinition,
  SettingTypes,
} from 'utils/types';
import DatasourceModel from 'cb-utils/models/datasource/DatasourceModel';
import { Map } from 'cb-utils/console-entity-models';
import { PARSER_TYPES } from 'containers/Portal/parserTypes';
import { BaseHTMLModel } from 'plugins/widgets/HTMLWidget/def';
import Port from 'cb-utils/models/PortalModel';
import { injectIntl } from 'react-intl';
import messages from './messages';

const portalModel = Port.getInstance();

interface IState {
  value: any;
}

interface IDatasourceEditorProps {
  currentValue: string | BaseHTMLModel;
  hasIncomingParser: boolean;
  hasOutgoingParser: boolean;
  isOutgoingParserDebugOn: boolean;
  isIncomingParserDebugOn: boolean;
  onDataTypeChange: (e: CBEvent) => void;
  incomingDatasources: IDatasourceSuggestions;
  outgoingDatasources: IDatasourceSuggestions;
  dynamicTypeConstant: string;
  staticTypeConstant: string;
  calculatedTypeConstant: string;
  settingModel: DataSettingDefinition;
  currentType: PARSER_TYPES;
  dataTypes: Array<{ value: PARSER_TYPES }>;
  datasourceInstances: Map<DatasourceModel>;
  currentDatasourceId: string;
  onCalculatedContentChange: (settingName: string, e: string) => void;
  onDatasourceChange: (e: CBEvent) => void;
  onDebugToggle: (idx: number, e: CBEvent) => void;
  onOpenParser: (sourceType: PARSER_TYPES, parserType?: string) => void;
}

interface DatasourceOptions {
  value: string;
  name: string;
  disabled?: boolean;
}

class DatasourceEditor extends React.Component<
  IDatasourceEditorProps & ReactIntl.InjectedIntlProps,
  IState
> {
  state: IState = {
    value: this.props.currentValue || '',
  };

  componentWillReceiveProps(nextProps: IDatasourceEditorProps) {
    this.setState({
      // The HTML widget returns an object, so if that specific case arises,
      // we stringify it to make a more meaningful static preview
      value:
        typeof nextProps.currentValue === 'object'
          ? JSON.stringify(nextProps.currentValue)
          : nextProps.currentValue,
    });
  }

  render() {
    const {
      settingModel,
      currentType,
      onDataTypeChange,
      dataTypes,
    } = this.props;
    return (
      <Col sm="12">
        <div style={{ marginBottom: '0.75rem' }}>
          <strong>{settingModel.display_name}</strong>
        </div>
        <Row>
          <CbInputField
            helpObj={this._buildHelpObj(settingModel)}
            type={SettingTypes.OPTION_TYPE}
            display_name=""
            labelText="Data Types"
            data-cy="chooseWidgetType"
            value={currentType}
            onChange={onDataTypeChange}
            name={settingModel.name}
            options={this._renderDataTypes(
              dataTypes,
              this.props.datasourceInstances,
            )}
          />
        </Row>
        {this._renderSourceFields(currentType)}
        <hr className="my-4" />
      </Col>
    );
  }

  _buildHelpObj = (settingModel: DataSettingDefinition) => {
    if (settingModel.incoming_parser) {
      return {
        description:
          'Choose type of incoming data to render. If dynamic, select the datasource within this portal to supply data to widget.',
        ...settingModel.helpObj,
      };
    } else if (settingModel.outgoing_parser) {
      return {
        description: 'Datasource in this portal to be affected by action.',
        ...settingModel.helpObj,
      };
    }
    return undefined;
  };

  _createDatasourceOptionsList = (instances: Map<DatasourceModel>) => {
    const rtn = [];

    for (const id in instances) {
      if (id) {
        rtn.push({ value: id, name: instances[id].name });
      }
    }

    return rtn;
  };

  _createPreferredOptionsList = (instances: IDatasourceSuggestions) => {
    if (Object.keys(instances.preferredInstances).length) {
      const preferred: DatasourceOptions[] = [
        {
          value: 'preferred',
          name: '---Preferred Datasources---',
          disabled: true,
        },
      ];
      const other: DatasourceOptions[] = [
        { value: 'other', name: '---Other Datasources---', disabled: true },
      ];
      preferred.push(
        ...this._createDatasourceOptionsList(instances.preferredInstances),
      );
      other.push(
        ...this._createDatasourceOptionsList(instances.otherInstances),
      );
      return preferred.concat(other);
    } else {
      return this._createDatasourceOptionsList(instances.otherInstances);
    }
  };

  _onDebugToggle = (idx: number, e: CBEvent) => {
    this.props.onDebugToggle(idx, e);
  };

  _openParser = (sourceType: PARSER_TYPES) => {
    this.props.onOpenParser(sourceType);
  };

  _openDynamicParser = (sourceType: PARSER_TYPES, parserType: string) => {
    this.props.onOpenParser(sourceType, parserType);
  };

  _renderDataTypes = (
    dataTypes: Array<{ value: PARSER_TYPES }>,
    datasourceInstances: Map<DatasourceModel>,
  ) => {
    let filteredTypes;
    if (Object.keys(datasourceInstances).length > 0) {
      filteredTypes = dataTypes;
    } else {
      filteredTypes = dataTypes.filter(
        (dataType: { value: PARSER_TYPES }) =>
          dataType.value !== this.props.dynamicTypeConstant,
      );
    }
    return filteredTypes.map((type: any, i: number) => {
      return { key: i, value: type.value, name: type.label };
    });
  };

  _renderSourceFields = (type: PARSER_TYPES) => {
    if (type === this.props.dynamicTypeConstant) {
      return this._renderDatasourceOptions();
    } else if (type === this.props.calculatedTypeConstant) {
      return this._renderCalculatedField();
    } else if (type === this.props.staticTypeConstant) {
      return this._renderStaticField();
    }
  };

  _renderCalculatedField = () => {
    return (
      <Row>
        <Col sm={9}>
          <Button
            onClick={this._openParser.bind(
              this,
              this.props.calculatedTypeConstant,
            )}
            color="primary"
          >
            <EditIcon />
            &nbsp; Edit calculated data
          </Button>
        </Col>
      </Row>
    );
  };

  _renderStaticField = () => {
    return (
      <Row>
        <Col sm={6}>
          <Button
            onClick={this._openParser.bind(this, this.props.staticTypeConstant)}
            color="primary"
          >
            <EditIcon />
            &nbsp; Edit static data
          </Button>
        </Col>
      </Row>
    );
  };

  _renderDatasourceOptions = () => {
    return (
      <Row key={this.props.currentDatasourceId}>
        <CbInputField
          type={SettingTypes.OPTION_TYPE}
          placeholder="Select datasource..."
          labelText="Datasource"
          useSelect
          name=""
          display_name=""
          {...this.getDatasourceError()}
          value={this.props.currentDatasourceId}
          onChange={this.props.onDatasourceChange}
          options={this.getDefaultDatasourceOptions().concat(
            this.props.hasIncomingParser
              ? this._createPreferredOptionsList(this.props.incomingDatasources)
              : this._createPreferredOptionsList(
                  this.props.outgoingDatasources,
                ),
          )}
        />
        <Col sm={12}>{this._renderParserButtons()}</Col>
      </Row>
    );
  };

  getDefaultDatasourceOptions = () => {
    if (!portalModel.hasDatasourceWithId(this.props.currentDatasourceId)) {
      return [
        {
          value: 'fakeId',
          name: this.props.intl.formatMessage(messages.selectDatasource),
        },
      ];
    }
    return [];
  };

  getDatasourceError = () => {
    const id = this.props.currentDatasourceId;
    if (!portalModel.hasDatasourceWithId(id)) {
      return {
        showError: true,
        error: this.props.intl.formatMessage(messages.badDsId, { id }),
      };
    }
    return {};
  };

  _renderParserButtons = () => {
    const rtn = [];

    if (this.props.hasIncomingParser) {
      rtn.push(
        <ParserButton
          key={0}
          title="Edit Parser"
          isDebugOn={this.props.isIncomingParserDebugOn}
          onDebugToggle={this._onDebugToggle.bind(this, INCOMING_PARSER)}
          onOpenParser={this._openDynamicParser.bind(
            this,
            this.props.dynamicTypeConstant,
            INCOMING_PARSER,
          )}
        />,
      );
    } else if (this.props.hasOutgoingParser) {
      rtn.push(
        <ParserButton
          key={1}
          title="Edit Parser"
          isDebugOn={this.props.isOutgoingParserDebugOn}
          onDebugToggle={this._onDebugToggle.bind(this, OUTGOING_PARSER)}
          onOpenParser={this._openDynamicParser.bind(
            this,
            this.props.dynamicTypeConstant,
            OUTGOING_PARSER,
          )}
        />,
      );
    }

    return rtn;
  };
}

export default injectIntl(DatasourceEditor);



