import { takeEvery } from 'redux-saga';
import { put, select, all } from 'redux-saga/effects';
import cloneDeep from 'lodash/cloneDeep';

import recommendChart, {
  IChartRecommendation,
} from 'cb-utils/chartRecommendation/chartRecommendation';

import { ActionConstants } from './actionConstants';

import {
  initWidgetSettingsSuccess,
  updateRecommendations,
  widgetUpdated,
  recommendationSelected,
  onOpenParser,
  onWidgetTypeChange,
  onCloseParserEditor,
  InitialWidgetSettings,
  ParserChanges,
  WidgetTypeChangeRequest,
  DataTypeChangeRequest,
  Recommendations,
  DatasourceChange,
  ParserUpdate,
  OpenParser,
  WidgetSettingChangeRequest,
  CloseParserEditorRequest,
  ImportWidgetRequest,
  CallWidgetSettingChangeInfo,
} from './actions';

import {
  selectCurrentParserType,
  selectCurrentParserSetting,
  selectCurrentParserSourceType,
  selectWidgetProps,
} from './selectors';

import {
  createPropsWithUpdatedParserValue,
  setDefaultValuesForSettings,
  setThemeableSettings,
  setupParsersForSource,
} from './utils';

import PortalModel from 'cb-utils/models/PortalModel';
import {
  HAS_UNIQUE_STYLE,
  HTML_WIDGET_COMPONENT,
  INCOMING_PARSER,
} from '../Portal/constants';
import { createExternalResourceListForWidget } from 'cb-utils/externalResourceUtils';
import { Map, AnyMap } from 'cb-utils/console-entity-models';
import { PARSER_TYPES, FORCE_DATA } from '../Portal/parserTypes';
import {
  DynamicDataSettingInstance,
  IDatasourceSuggestions,
  IParserInfo,
  DataSettingInstanceTypes,
  PluginSettingDefinitionTypes,
  PluginSettingDefinitions,
  DataSettingDefinition,
} from 'utils/types';
import WidgetModel from 'cb-utils/models/widget/WidgetModel';
import DatasourceModel from 'cb-utils/models/datasource/DatasourceModel';
import { BaseAction } from 'cb-utils/baseAction';
import { copyJsonObject } from 'cb-utils/copyJsonObject';
import { PluginInfo } from 'cb-utils/models/plugin/PluginModel';
import { WidgetTypes } from 'plugins/widgets/types';

const portalModel = PortalModel.getInstance();
let widgetInstance: WidgetModel<{}>;
let WIDGET_SETTINGS_CACHE: PluginInfo;

export function getWidgetSettingsCache() {
  return WIDGET_SETTINGS_CACHE;
}

export function setWidgetSettingsCache(val: PluginInfo) {
  WIDGET_SETTINGS_CACHE = val;
  return WIDGET_SETTINGS_CACHE;
}

export function getWidgetInstance() {
  return widgetInstance;
}

export function setWidgetInstance(val: WidgetModel<{}>) {
  if (widgetInstance) {
    widgetInstance.latestData.unsubscribe(handleWidgetDataUpdate); // we need to unsubscribe if we have a widgetInstance to avoid more than one subscription
  }
  widgetInstance = val;
  if (widgetInstance) {
    widgetInstance.latestData.subscribe(handleWidgetDataUpdate);
  }
  return widgetInstance;
}

function callCleanUpTempWidget(action: BaseAction) {
  try {
    setWidgetInstance(null);
    portalModel.cleanUpTempWidget();
  } catch (e) {
    // tslint:disable-next-line:no-console
    console.error('Failed to clean up temp widget', action.payload, e);
  }
}

function handleWidgetDataUpdate(data: AnyMap) {
  // todo: make this dynamic based on the type of widget rather than checking for a particular setting
  if (data && data.chartData) {
    let recommendations: IChartRecommendation[] = [];
    try {
      const i = getWidgetInstance();
      recommendations = recommendChart(i.type as WidgetTypes, data.chartData);
    } catch (e) {
      // todo: let user know that their data is incompatible
    }
    portalModel.dispatch(updateRecommendations(recommendations));
  }
}

function* callInitWidgetSettings(action: InitialWidgetSettings) {
  try {
    const widgetTypes = portalModel.getWidgetTypes();
    const currentWidgetInfo = action.payload.widgetInfo;
    const currentWidgetType = currentWidgetInfo.type;
    const settingsForWidgetType = widgetTypes[currentWidgetType].settings;
    const widgetThemes = action.payload.widgetThemes;
    const themeForWidgetType = widgetThemes[currentWidgetType];
    const incomingDatasources = portalModel.getIncomingDatasourcesForWidget(
      settingsForWidgetType,
    );
    const outgoingDatasources = portalModel.getOutgoingDatasourcesForWidget(
      settingsForWidgetType,
    );
    const currentWidgetSettings = setThemeableSettings(
      setDefaultValuesForSettings(
        settingsForWidgetType,
        currentWidgetInfo.props,
        incomingDatasources,
        outgoingDatasources,
      ),
      themeForWidgetType,
    );
    setWidgetInstance(
      portalModel.createTemporaryWidget(
        currentWidgetInfo.id,
        currentWidgetType,
        currentWidgetSettings,
        action.payload.widgetInfo.externalScripts,
      ),
    );
    const externalResourceListForWidget = createExternalResourceListForWidget(
      action.payload.externalScripts,
      action.payload.widgetInfo.externalScripts,
    );
    yield put(
      initWidgetSettingsSuccess(widgetThemes, externalResourceListForWidget),
    );
  } catch (e) {
    // tslint:disable-next-line:no-console
    console.error('Failed to call init widget settings', action.payload, e);
  }
}

function* callUpdateWidgetSettings() {
  updateWidgetSettings(yield select(selectWidgetProps()));
  yield dispatchWidgetUpdate();
}

function updateWidgetSettings(newSettings: PluginInfo) {
  const i = getWidgetInstance();
  i.settings(newSettings);
  i.processUpdateToSettings();
  setWidgetInstance(i);
}

function* dispatchWidgetUpdate(type = '') {
  yield put(widgetUpdated(type, Date.now()));
}

function* callTestParserChanges(action: ParserChanges) {
  const parserSourceType = yield select(selectCurrentParserSourceType());
  const parserSetting = yield select(selectCurrentParserSetting());
  const parserType = yield select(selectCurrentParserType());
  const i = getWidgetInstance();
  const currentWidgetSettings = i.settings();
  const newWidgetSettings: PluginInfo = createPropsWithUpdatedParserValue(
    parserSourceType,
    parserSetting,
    parserType,
    currentWidgetSettings,
    action.payload,
  );
  updateWidgetSettings(newWidgetSettings);
  yield dispatchWidgetUpdate(action.type);
}

function* callEditWidget(action: WidgetTypeChangeRequest) {
  const widgetType = action.payload.value;
  const newWidgetTypeDefinition = portalModel.getWidgetTypeById(widgetType);
  const settingsForWidget = newWidgetTypeDefinition.settings;
  const incomingDatasources = portalModel.getIncomingDatasourcesForWidget(
    settingsForWidget,
  );
  const outgoingDatasources = portalModel.getOutgoingDatasourcesForWidget(
    settingsForWidget,
  );
  const themeForWidget = action.payload.widgetThemes[widgetType];
  let newSettings = setThemeableSettings(
    setDefaultValuesForSettings(
      settingsForWidget,
      {},
      incomingDatasources,
      outgoingDatasources,
    ),
    themeForWidget,
  );
  const i = setWidgetInstance(
    yield portalModel.editTemporaryWidget(
      widgetType,
      newWidgetTypeDefinition,
      newSettings,
    ),
  );
  if (newWidgetTypeDefinition.onTypeChange) {
    newSettings = newWidgetTypeDefinition.onTypeChange(
      copyJsonObject(newSettings),
      newWidgetTypeDefinition,
      getWidgetInstance(),
    );
    i.settings(newSettings);
  }
  yield put(onWidgetTypeChange());
  handleWidgetDataUpdate(getWidgetInstance().latestData());
}

function getPreferredParser(suggestions: IDatasourceSuggestions, dsId: string) {
  return suggestions.preferredInstances[dsId].defaultParser
    ? suggestions.preferredInstances[dsId].defaultParser
    : undefined;
}

function getPreferredSettings(
  suggestions: IDatasourceSuggestions,
  dsId: string,
) {
  return suggestions.preferredInstances[dsId].defaultSettings
    ? suggestions.preferredInstances[dsId].defaultSettings
    : {};
}

function* handleDataTypeChange(action: DataTypeChangeRequest) {
  const settingName = action.payload.settingName;
  const i = getWidgetInstance();
  const currentSettings = i.settings();
  const settingDefinitions = i.getSettingsForDefinition();
  const currentSettingDef = i.getDefinitionForSettingByName(settingName);

  const setting = currentSettings[settingName];
  const forceDataDataType = setting.force_data;
  const value = action.payload.value;
  let newSetting: DataSettingInstanceTypes;
  let incomingSettings: PluginInfo;
  let outgoingSettings: PluginInfo;

  if (value === PARSER_TYPES.DYNAMIC) {
    if ((currentSettingDef as DataSettingDefinition).incoming_parser) {
      const incomingDatasources: IDatasourceSuggestions = portalModel.getIncomingDatasourcesForWidget(
        settingDefinitions,
      );
      if (Object.keys(incomingDatasources.preferredInstances).length) {
        newSetting = {
          ...newSetting,
          dataType: value,
          id: Object.keys(incomingDatasources.preferredInstances)[0],
        };
        const parser = getPreferredParser(incomingDatasources, newSetting.id);
        incomingSettings = getPreferredSettings(
          incomingDatasources,
          newSetting.id,
        );
        newSetting = {
          ...newSetting,
          ...setupParsersForSource(
            settingName,
            settingDefinitions,
            forceDataDataType,
            parser,
          ),
        };
      } else {
        const parser =
          i.type === HTML_WIDGET_COMPONENT ? setting.value : undefined;
        newSetting = {
          ...newSetting,
          dataType: value,
          id: Object.keys(incomingDatasources.otherInstances)[0],
          ...setupParsersForSource(
            settingName,
            settingDefinitions,
            forceDataDataType,
            parser,
          ),
        };
      }
    } else if ((currentSettingDef as DataSettingDefinition).outgoing_parser) {
      const outgoingDatasources: IDatasourceSuggestions = portalModel.getOutgoingDatasourcesForWidget(
        settingDefinitions,
      );
      if (Object.keys(outgoingDatasources.preferredInstances).length) {
        newSetting = {
          ...newSetting,
          dataType: value,
          id: Object.keys(outgoingDatasources.preferredInstances)[0],
        };
        const parser = getPreferredParser(outgoingDatasources, newSetting.id);
        outgoingSettings = getPreferredSettings(
          outgoingDatasources,
          newSetting.id,
        );
        newSetting = {
          ...newSetting,
          ...setupParsersForSource(
            settingName,
            settingDefinitions,
            forceDataDataType,
            parser,
          ),
        };
      } else {
        newSetting = handleUnpreferredDatasource(
          setting,
          newSetting,
          settingName,
          Object.keys(outgoingDatasources.otherInstances)[0],
          settingDefinitions,
          forceDataDataType,
          i,
        );
      }
    }
  } else if (
    value === PARSER_TYPES.CALCULATED ||
    value === PARSER_TYPES.STATIC
  ) {
    const parserValue =
      i.type === HTML_WIDGET_COMPONENT
        ? setting[INCOMING_PARSER].value || setting.value
        : i.getDefaultValueForSettingByName(settingName, value);
    // @ts-ignore
    newSetting = {
      dataType: value,
      value: parserValue,
      force_data: forceDataDataType || null,
    };
  }

  currentSettings[settingName] = newSetting;
  if (incomingSettings && Object.keys(incomingSettings).length) {
    for (const field in incomingSettings) {
      if (incomingSettings.hasOwnProperty(field)) {
        currentSettings[field] = incomingSettings[field];
      }
    }
  }
  if (outgoingSettings && Object.keys(outgoingSettings).length) {
    for (const field in outgoingSettings) {
      if (outgoingSettings.hasOwnProperty(field)) {
        currentSettings[field] = outgoingSettings[field];
      }
    }
  }

  updateWidgetSettings(currentSettings);
  yield dispatchWidgetUpdate(action.type);
}

function* callRecommendationSelected(action: Recommendations) {
  getWidgetInstance().settings(action.payload);
  yield put(recommendationSelected());
}

function* handleDatasourceChange(action: DatasourceChange) {
  const i = getWidgetInstance();
  const currentSettings = i.settings();
  const settingsForDefinition = i.getSettingsForDefinition();
  const settingName = action.payload.settingName;
  const currentSetting = currentSettings[settingName];
  const forceDataDatasource = currentSetting.force_data;
  let newSetting: DynamicDataSettingInstance;
  let preferredDatasourceSettings: Map<DatasourceModel>;

  if (currentSetting[INCOMING_PARSER]) {
    const incomingDatasources: IDatasourceSuggestions = portalModel.getIncomingDatasourcesForWidget(
      settingsForDefinition,
    );
    if (
      Object.keys(incomingDatasources.preferredInstances).length &&
      incomingDatasources.preferredInstances[action.payload.value]
    ) {
      newSetting = {
        ...newSetting,
        dataType: PARSER_TYPES.DYNAMIC,
        id: action.payload.value,
      };
      const parser = getPreferredParser(incomingDatasources, newSetting.id);
      preferredDatasourceSettings = getPreferredSettings(
        incomingDatasources,
        newSetting.id,
      );
      newSetting = {
        ...newSetting,
        ...setupParsersForSource(
          settingName,
          settingsForDefinition,
          forceDataDatasource,
          parser,
        ),
      };
    } else {
      newSetting = handleUnpreferredDatasource(
        currentSetting,
        newSetting,
        settingName,
        action.payload.value,
        settingsForDefinition,
        forceDataDatasource,
        i,
      );
    }
  } else {
    const outgoingDatasources: IDatasourceSuggestions = portalModel.getOutgoingDatasourcesForWidget(
      settingsForDefinition,
    );
    if (
      Object.keys(outgoingDatasources.preferredInstances).length &&
      outgoingDatasources.preferredInstances[action.payload.value]
    ) {
      newSetting = {
        ...newSetting,
        dataType: PARSER_TYPES.DYNAMIC,
        id: action.payload.value,
      };
      const parser = getPreferredParser(outgoingDatasources, newSetting.id);
      preferredDatasourceSettings = getPreferredSettings(
        outgoingDatasources,
        newSetting.id,
      );
      newSetting = {
        ...newSetting,
        ...setupParsersForSource(
          settingName,
          settingsForDefinition,
          forceDataDatasource,
          parser,
        ),
      };
    } else {
      newSetting = handleUnpreferredDatasource(
        currentSetting,
        newSetting,
        settingName,
        action.payload.value,
        settingsForDefinition,
        forceDataDatasource,
        i,
      );
    }
  }

  currentSettings[settingName] = newSetting;
  if (
    preferredDatasourceSettings &&
    Object.keys(preferredDatasourceSettings).length
  ) {
    for (const setting in preferredDatasourceSettings) {
      if (preferredDatasourceSettings.hasOwnProperty(setting)) {
        currentSettings[setting] = preferredDatasourceSettings[setting];
      }
    }
  }
  callWidgetSettingChange(action.payload, currentSettings);
  yield dispatchWidgetUpdate(action.type);
}

function handleUnpreferredDatasource(
  currentSetting: DynamicDataSettingInstance,
  newSetting: DataSettingInstanceTypes,
  settingName: string,
  id: string,
  settingsForDefinition: PluginSettingDefinitions,
  forceDataDatasource: FORCE_DATA,
  instance: WidgetModel<{}>,
): DynamicDataSettingInstance {
  const parser =
    instance.type === HTML_WIDGET_COMPONENT
      ? currentSetting[INCOMING_PARSER].value
      : undefined;
  return {
    ...newSetting,
    dataType: PARSER_TYPES.DYNAMIC,
    id,
    ...setupParsersForSource(
      settingName,
      settingsForDefinition,
      forceDataDatasource,
      parser,
    ),
  };
}

function* handleDebugToggle(action: ParserUpdate) {
  const i = getWidgetInstance();
  const currentSettings = i.settings();
  const settingName = action.payload.settingName;
  const parserType = action.payload.parserType;
  currentSettings[settingName][parserType].isDebugOn = !currentSettings[
    settingName
  ][parserType].isDebugOn;
  i.settings(currentSettings);

  // if the user turned on debug, let's process the update
  if (currentSettings[settingName][parserType].isDebugOn) {
    i.processUpdateToSettings();
  }

  yield put(widgetUpdated(action.type, Date.now()));
}

export function* handleOpenParser(action: OpenParser) {
  let script;
  const widgetSettingsCache: PluginInfo = setWidgetSettingsCache(
    cloneDeep(getWidgetInstance().settings()),
  );
  const settingName = action.payload.settingName;
  const parserType = action.payload.parserType;
  if (action.payload.sourceType === PARSER_TYPES.DYNAMIC) {
    script = (widgetSettingsCache[settingName][parserType] as IParserInfo)
      .value;
  } else {
    script = widgetSettingsCache[settingName].value;
  }
  yield put(
    onOpenParser(settingName, action.payload.sourceType, parserType, script),
  );
}

function* handleWidgetSettingChange(action: WidgetSettingChangeRequest) {
  const settingName = action.payload.settingName;
  const value = action.payload.value;
  const i = getWidgetInstance();
  let currentSettings = {
    ...i.settings(),
    [settingName]: value,
  };
  if (settingName === HAS_UNIQUE_STYLE && value === false) {
    const themeForWidget = action.payload.widgetThemes[i.definition.type_name];
    currentSettings = {
      ...currentSettings,
      ...setThemeableSettings(currentSettings, themeForWidget),
    };
  }
  if (action.payload.setting.isThemeable) {
    currentSettings = {
      ...currentSettings,
      [HAS_UNIQUE_STYLE]: true,
    };
  }
  callWidgetSettingChange(action.payload, currentSettings);
  yield put(widgetUpdated(action.type, Date.now()));
}

function* handleCloseParserEditor(action: CloseParserEditorRequest) {
  const currentParserSourceType = yield select(selectCurrentParserSourceType());
  const currentParserSetting = yield select(selectCurrentParserSetting());
  const currentParserType = yield select(selectCurrentParserType());
  const currentSettings = getWidgetInstance().settings();
  const newSettings = createPropsWithUpdatedParserValue(
    currentParserSourceType,
    currentParserSetting,
    currentParserType,
    currentSettings,
    action.payload.value,
  );
  updateWidgetSettings(newSettings);
  yield dispatchWidgetUpdate(action.type);
  yield put(onCloseParserEditor());
}

function callWidgetSettingChange(
  payload: CallWidgetSettingChangeInfo,
  currentSettings: PluginInfo,
) {
  const i = getWidgetInstance();
  const settingName = payload.settingName;
  const currentSettingDefinition = i
    .getSettingsForDefinition()
    .find((value: PluginSettingDefinitionTypes) => {
      return value.name === settingName;
    });
  let newSettings = currentSettings;
  if (currentSettingDefinition.onSettingChange) {
    newSettings = {
      ...newSettings,
      ...currentSettingDefinition.onSettingChange(
        copyJsonObject(currentSettings),
        payload.value,
        payload.data,
        currentSettings,
      ),
    };
  }
  updateWidgetSettings(newSettings);
  return newSettings;
}

export function* handleDismissParserEditor(action: BaseAction) {
  // we need to discard any changes to the settings whenever the parser editor is dismissed
  updateWidgetSettings(getWidgetSettingsCache());
  yield dispatchWidgetUpdate(action.type);
}

function* cleanUpTempWidgetSaga() {
  yield takeEvery(ActionConstants.CLEAN_UP_TEMP_WIDGET, callCleanUpTempWidget);
}

function* initWidgetSettings() {
  yield takeEvery(
    ActionConstants.INIT_WIDGET_SETTINGS_REQUEST,
    callInitWidgetSettings,
  );
}

function* updateWidgetSettingsSaga() {
  yield takeEvery(
    [
      ActionConstants.CALCULATED_CONTENT_CHANGE,
      ActionConstants.ON_CLOSE_PARSER_EDITOR,
      ActionConstants.ON_DEBUG_TOGGLE,
    ],
    callUpdateWidgetSettings,
  );
}

function* dismissParserSaga() {
  yield takeEvery(
    ActionConstants.DISMISS_PARSER_EDITOR,
    handleDismissParserEditor,
  );
}

function* testParserChanges() {
  yield takeEvery(ActionConstants.TEST_PARSER_CHANGES, callTestParserChanges);
}

function* widgetTypeChange() {
  yield takeEvery(
    ActionConstants.ON_WIDGET_TYPE_CHANGE_REQUEST,
    callEditWidget,
  );
}

function* dataTypeChangeRequestSaga() {
  yield takeEvery(
    ActionConstants.ON_DATA_TYPE_CHANGE_REQUEST,
    handleDataTypeChange,
  );
}

function* recommendationSelectedRequestSaga() {
  yield takeEvery(
    ActionConstants.RECOMMENDATION_SELECTED_REQUEST,
    callRecommendationSelected,
  );
}

function* datasourceChangeSaga() {
  yield takeEvery(ActionConstants.DATASOURCE_CHANGE, handleDatasourceChange);
}

function* debugToggleSaga() {
  yield takeEvery(ActionConstants.ON_DEBUG_TOGGLE, handleDebugToggle);
}

function* openParserRequestSaga() {
  yield takeEvery(ActionConstants.ON_OPEN_PARSER_REQUEST, handleOpenParser);
}

function* widgetSettingChangeRequestSaga() {
  yield takeEvery(
    ActionConstants.ON_WIDGET_SETTING_CHANGE_REQUEST,
    handleWidgetSettingChange,
  );
}

function* closeParserEditorRequestSaga() {
  yield takeEvery(
    ActionConstants.ON_CLOSE_PARSER_EDITOR_REQUEST,
    handleCloseParserEditor,
  );
}

function* importWidgetSaga() {
  yield takeEvery(ActionConstants.IMPORT_WIDGET_REQUEST, function*(
    action: ImportWidgetRequest,
  ) {
    yield callEditWidget({
      type: ActionConstants.ON_WIDGET_TYPE_CHANGE_REQUEST,
      payload: {
        value: action.payload.widget.type,
        widgetThemes: action.payload.widgetThemes,
      },
    });
    updateWidgetSettings(action.payload.widget.props);
    yield dispatchWidgetUpdate(ActionConstants.IMPORT_WIDGET_SUCCESS);
  });
}

export default function*() {
  yield all([
    cleanUpTempWidgetSaga(),
    initWidgetSettings(),
    testParserChanges(),
    widgetTypeChange(),
    updateWidgetSettingsSaga(),
    dataTypeChangeRequestSaga(),
    recommendationSelectedRequestSaga(),
    datasourceChangeSaga(),
    debugToggleSaga(),
    openParserRequestSaga(),
    widgetSettingChangeRequestSaga(),
    closeParserEditorRequestSaga(),
    dismissParserSaga(),
    importWidgetSaga(),
  ]);
}



