/**
 *
 * ParserHelp
 *
 */

import React from 'react';
import styled from 'styled-components';

import { Collapse, Card, CardBody, CardHeader } from 'reactstrap';
import { FormattedHTMLMessage } from 'react-intl';

import { OUTGOING_PARSER } from '../../containers/Portal/constants';

import CollapsibleAreaTitle from '../CollapsibleAreaTitle';

import messages from './messages';
import { PARSER_TYPES } from 'containers/Portal/parserTypes';
import withStyledProps from 'cb-utils/withStyledProps';

const ScrollableDiv = styled.div`
  overflow-y: scroll;
  padding: 5px;
  max-height: 25vh;
  border-right: 1px solid #e5e5e5;
`;

const CardHeaderWrapper = withStyledProps<{ onClick: () => void }>()(
  styled(CardHeader),
)`
  padding: 0.1em 1em;
  cursor: pointer;
`;

interface IParserHelpProps {
  showCollapsed: boolean;
  currentParserSourceType: string;
  currentParserType: string;
  expectedFormatTips: string;
}

interface IParserHelpState {
  collapseHelp: boolean;
  collapseFormat: boolean;
}

class ParserHelp extends React.PureComponent<IParserHelpProps, {}> {
  state: IParserHelpState = {
    collapseHelp: true,
    collapseFormat: true,
  };

  _getHelpText = () => {
    let helpText;

    switch (this.props.currentParserSourceType) {
      case PARSER_TYPES.DYNAMIC:
        if (this.props.currentParserType === OUTGOING_PARSER) {
          helpText = messages.outgoingDynamicHelp;
        } else {
          helpText = messages.incomingDynamicHelp;
        }
        break;
      case PARSER_TYPES.STATIC:
        if (this.props.currentParserType === OUTGOING_PARSER) {
          helpText = messages.outgoingStaticHelp;
        } else {
          helpText = messages.incomingStaticHelp;
        }
        break;
      case PARSER_TYPES.CALCULATED:
        if (this.props.currentParserType === OUTGOING_PARSER) {
          helpText = messages.outgoingCalculatedHelp;
        } else {
          helpText = messages.incomingCalculatedHelp;
        }
        break;
      default:
        break;
    }

    return helpText;
  };

  _toggleHelp = () => {
    this.setState({ collapseHelp: !this.state.collapseHelp });
  };

  _toggleFormat = () => {
    this.setState({ collapseFormat: !this.state.collapseFormat });
  };

  render() {
    return (
      <Collapse isOpen={!this.props.showCollapsed}>
        <Card>
          <CollapsibleAreaTitle
            tag={CardHeaderWrapper}
            title="Help"
            isOpen={!this.state.collapseHelp}
            onClick={this._toggleHelp}
          />

          <Collapse isOpen={!this.state.collapseHelp}>
            <CardBody>
              <ScrollableDiv>
                <FormattedHTMLMessage {...this._getHelpText()} />
              </ScrollableDiv>
            </CardBody>
          </Collapse>
        </Card>
        {this.props.expectedFormatTips && (
          <Card>
            <CollapsibleAreaTitle
              tag={CardHeaderWrapper}
              title="Expected Format Tips"
              isOpen={!this.state.collapseFormat}
              onClick={this._toggleFormat}
            />

            <Collapse isOpen={!this.state.collapseFormat}>
              <CardBody>
                <ScrollableDiv
                  dangerouslySetInnerHTML={{
                    __html: this.props.expectedFormatTips,
                  }}
                />
              </CardBody>
            </Collapse>
          </Card>
        )}
      </Collapse>
    );
  }
}

export default ParserHelp;



