/*
 * ParserHelp Messages
 *
 * This contains all the text for the ParserHelp component.
 */
import { defineMessages } from 'react-intl';

export default defineMessages({
  incomingStaticHelp: {
    id: 'app.components.ParserHelp.incomingStatic',
    defaultMessage:
      'A static datasource consists of raw text that is not executed by a Javascript function. Any text that is added to the editor will become the value of the widget setting the datasource is associated with.',
  },
  outgoingStaticHelp: {
    id: 'app.components.ParserHelp.outgoingStatic',
    defaultMessage:
      'A static datasource consists of raw text that is not executed by a Javascript function. Any text that is added to the editor will become the value of the widget setting the datasource is associated with.',
  },
  incomingCalculatedHelp: {
    id: 'app.components.ParserHelp.incomingCalculated',
    defaultMessage:
      'A calculated datasource is a javaScript function that is not bound to a defined portal datasource. The data specified by the <code><span class="cm-keyword">return</span></code> statement will become the value of the widget setting the datasource is associated with. You have access to the following: <br><br> 1) A variable named <code><span class="cm-keyword">datasources</span></code> <br> &nbsp;&nbsp; Use this to access any datasource within your portal. e.g., <code><span class="cm-keyword">datasources["dataSourceName"].latestData()</span></code> <br> 2) A variable named <code><span class="cm-keyword">panes</span></code> <br> &nbsp;&nbsp;&nbsp;&nbsp; Use this to access any pane within your portal. e.g., <code><span class="cm-keyword">panes["uniquePaneId"].updateCurrentTab(2)</span></code>',
  },
  outgoingCalculatedHelp: {
    id: 'app.components.ParserHelp.outgoingCalculated',
    defaultMessage:
      'A calculated datasource is a javaScript function that is not bound to a defined portal datasource. The data specified by the <code><span class="cm-keyword">return</span></code> statement will become the value of the widget setting the datasource is associated with. You have access to the following: <br><br> 1) A variable named <code><span class="cm-keyword">datasources</span></code> <br> &nbsp;&nbsp; Use this to access any datasource within your portal. e.g., <code><span class="cm-keyword">datasources["dataSourceName"].latestData()</span></code> <br> 2) A variable named <code><span class="cm-keyword">panes</span></code> <br> &nbsp;&nbsp;&nbsp;&nbsp; Use this to access any pane within your portal. e.g., <code><span class="cm-keyword">panes["uniquePaneId"].updateCurrentTab(2)</span></code>',
  },
  incomingDynamicHelp: {
    id: 'app.components.ParserHelp.incomingDynamic',
    defaultMessage:
      'A dynamic incoming datasource is a javascript function that will be re-evaluated any time the corresponding datasource is updated. The value you <code><span class="cm-keyword">return</span></code> will become the value of the widget setting the datasource is associated with. You have access to the following: <br><br> 1) The <code><span class="cm-keyword">this</span></code> keyword which has the following properties - <br> &nbsp;&nbsp; <code><span class="cm-keyword">widget</span></code> (widget that uses the datasource) <br> &nbsp;&nbsp;&nbsp;&nbsp;i. Use this to retrieve the current value for your widget, e.g., <code><span class="cm-keyword">this.widget</span></code> <br> &nbsp;&nbsp;&nbsp;&nbsp; <code><span class="cm-keyword">datasource</span></code> (datasource which triggered the parser) <br> &nbsp;&nbsp;&nbsp;&nbsp;i. Use this to retrieve the current value for your datasource, e.g., <code><span class="cm-keyword">this.datasource</span></code>  <br> &nbsp;&nbsp; <code><span class="cm-keyword">setCustomDimensions</span></code> (method that resizes widget) <br> &nbsp;&nbsp;&nbsp;&nbsp;i. Use this to resize the current widget based on data changes. It accepts two arguments (width, height). Width and height parameters are measured in grid units, the number of rows or columns occupied by the widget, e.g., <code><span class="cm-keyword">this.setCustomDimensions(2, 1)</span></code> <br>  2) A variable named <code><span class="cm-keyword">datasources</span></code> <br> &nbsp;&nbsp; Use this to access any datasource within your portal. e.g., <code><span class="cm-keyword">datasources["dataSourceName"].latestData()</span></code> <br> 3) A variable named <code><span class="cm-keyword">panes</span></code> <br> &nbsp;&nbsp;&nbsp;&nbsp; Use this to access any pane within your portal. e.g., <code><span class="cm-keyword">panes["uniquePaneId"].updateCurrentTab(2)</span></code> <br> &nbsp;&nbsp;&nbsp;&nbsp; A setCustomDimensions method can also be applied to panes. e.g., <code><span class="cm-keyword">panes["uniquePaneId"].setCustomDimensions(2, 1))</span></code>',
  },
  outgoingDynamicHelp: {
    id: 'app.components.ParserHelp.outgoingDynamic',
    defaultMessage:
      'A dynamic outgoing datasource is a javascript function that will be re-evaluated any time this widget\'s state changes. The value you <code><span class="cm-keyword">return</span></code> will become the value of the widget setting the datasource is associated with. You have access to the following: <br> 1) The <code><span class="cm-keyword">this</span></code> keyword which has the following properties - <br> &nbsp;&nbsp; <code><span class="cm-keyword">widget</span></code> (widget that uses the datasource) <br> &nbsp;&nbsp;i. Use this to retrieve the current value for your widget, e.g., <code><span class="cm-keyword">this.widget</span></code> <br> &nbsp;&nbsp;&nbsp;&nbsp; <code><span class="cm-keyword">datasource</span></code> (datasource which triggered the parser) <br> &nbsp;&nbsp;&nbsp;&nbsp;i. Use this to retrieve the current value for your datasource, e.g., <code><span class="cm-keyword">this.datasource</span></code>  <br>  2) A variable named <code><span class="cm-keyword">datasources</span></code> <br> &nbsp;&nbsp; Use this to access any datasource within your portal. e.g., <code><span class="cm-keyword">datasources["dataSourceName"].latestData()</span></code> <br> 3) A variable named <code><span class="cm-keyword">panes</span></code> <br> &nbsp;&nbsp;&nbsp;&nbsp; Use this to access any pane within your portal. e.g., <code><span class="cm-keyword">panes["uniquePaneId"].updateCurrentTab(2)</span></code>',
  },
});



