import AbstractEditorModel from '../editorModels/AbstractEditorModel';
import PortalModel from '../../../cb-utils/models/PortalModel';

///////////////////////////////////////////////////////////////////////
// NOTE
//
// IDE's like VSCode will show errors for the .d.ts imports.
// The imports will still work, however. Ignore the errors in the editor
///////////////////////////////////////////////////////////////////////

class CbParserModel extends AbstractEditorModel {
  static portalModel = PortalModel.getInstance();

  public getModels(): string {
    return (
      this.createPortalModel() +
      this.createDatasourcesModel() +
      this.createPluginModel() +
      this.createDatasourceModel()
    );
  }

  private createPortalModel(): string {
    return `interface Loader {
      show(htmlId: string): void;
      hide(htmlId: string): void;
      waitFor(promise: Promise, htmlId: string): void;
    }
    
    static class CB_PORTAL {
       static ClearBlade: ClearBladeInt;
       static Loader: Loader;
       static getPathParams: getPathParams();
       static portalModel: PortalModel;
       static registerDatasource(datasource: DatasourceModel): void;
       static registerWidget(widget: WidgetModel): void;
       static selectPage(path: string): void;
    }`;
  }

  private createPluginModel(): string {
    return `interface CbPlugin {
         id: string
         type: string
         settings: CbObservable
         latestData: CbObservable
         lastUpdated: Date
         edit(settings: any): void;
         getSettingByName(settingName: string) : any
      }`;
  }

  private createDatasourceModel(): string {
    return `interface DatasourceModel extends CbPlugin {
         edit(settings: any, name: any): void;
         getName(): any;
         getSchema(): any;
         refresh(): void;
         delete(): void;
         sendData(data: any): any;
      }`;
  }

  private createDatasourcesModel = (): string => {
    const datasourcesClass = ['class datasources {'];
    if (!!CbParserModel.portalModel.datasourceInstances) {
      const instances = CbParserModel.portalModel.datasourceInstances;

      for (const key of Object.keys(instances)) {
        // Object.values is in ES2017
        // for (var ds of Object.values(AbstractEditorModel.portalModel["datasourceInstances"])) {
        datasourcesClass.push(
          'static ' + instances[key].name + ': DatasourceModel,',
        );
      }
    }

    // End the class
    datasourcesClass.push('}');
    return datasourcesClass.join('\n');
  };
}

export default CbParserModel;



