/**
 *
 * CbChartRecommendation
 *
 */

import React from 'react';

import CbWidget, { PluginWidgetComponentTypes } from '../CbWidget';
import styled from 'styled-components';

import { MULTI_OBJECT_SETTING_TYPE } from '../../containers/Portal/constants';
import { Button, Card, CardBody, CardText } from 'reactstrap';
import {
  IChartRecommendation,
  IChartPossibility,
} from 'cb-utils/chartRecommendation/chartRecommendation';
import { AnyMap } from 'cb-utils/console-entity-models';
import {
  PluginSettingDefinitionTypes,
  MultiObjectSettingDefinition,
} from 'utils/types';
import WidgetModel from 'cb-utils/models/widget/WidgetModel';

const CardRecommendation = styled(Card)`
  && {
    margin-bottom: 40px;
    border-radius: 0;
    border-bottom: 0;
    box-shadow: 0 5px 10px rgba(0, 0, 0, 0.19);
  }
`;

const CardRecommendationBody = styled(CardBody)`
  margin-right: 40px;
  height: 250px;
`;

const SelectButton = styled(Button)`
  && {
    border-radius: 0;
    font-size: 22px;
  }
`;

interface IProps {
  recommendations: IChartRecommendation[];
  settings: AnyMap;
  widgetDefinition: PluginSettingDefinitionTypes[];
  data: AnyMap;
  type: PluginWidgetComponentTypes;
  recommendationSelected: (settings: AnyMap) => void;
}

class CbChartRecommendation extends React.PureComponent<IProps, {}> {
  render() {
    return (
      <div>
        {this.renderRecommendations(
          this.props.recommendations,
          this.props.settings,
          this.props.widgetDefinition,
          this.props.type,
          this.props.data,
        )}
      </div>
    );
  }

  createNewSetting(
    def: PluginSettingDefinitionTypes,
    recommendation: IChartPossibility,
  ) {
    if (def.xRecommendation) {
      return recommendation.x;
    } else if (def.yRecommendation) {
      return recommendation.y;
    }
  }

  mergeRecommendationsWithWidgetSettings(
    recommendation: IChartPossibility,
    settings: AnyMap,
    widgetDefinition: PluginSettingDefinitionTypes[],
  ) {
    let newSettings = {
      ...settings,
    };
    widgetDefinition.forEach((def) => {
      if (def.type === MULTI_OBJECT_SETTING_TYPE) {
        (def as MultiObjectSettingDefinition).objectShape.forEach(
          (nestedDef) => {
            if (nestedDef.yRecommendation) {
              const newSettingsForMulti = recommendation.y.map((el, i) => {
                if (newSettings[def.name] && newSettings[def.name][i]) {
                  return {
                    ...newSettings[def.name][i],
                    [nestedDef.name]: el,
                  };
                } else {
                  return {
                    [nestedDef.name]: el,
                  };
                }
              });

              if (newSettingsForMulti) {
                newSettings = {
                  ...newSettings,
                  [def.name]: newSettingsForMulti,
                };
              }
            }
          },
        );
      } else {
        const newSetting = this.createNewSetting(def, recommendation);
        if (newSetting) {
          newSettings = {
            ...newSettings,
            [def.name]: newSetting,
          };
        }
      }
    });
    return newSettings;
  }

  recommendationSelected(settings: AnyMap) {
    this.props.recommendationSelected(settings);
  }

  renderRecommendations(
    recommendations: IChartRecommendation[],
    settings: AnyMap,
    widgetDefinition: PluginSettingDefinitionTypes[],
    widgetType: PluginWidgetComponentTypes,
    data: AnyMap,
  ) {
    if (recommendations && recommendations.length > 0) {
      return recommendations.map((rec) => {
        return rec.possibilities.map((poss, idx) => {
          const recommendedSettings = this.mergeRecommendationsWithWidgetSettings(
            poss,
            settings,
            widgetDefinition,
          );
          return (
            <CardRecommendation key={idx}>
              <CardRecommendationBody>
                <CbWidget
                  id={'chartRecommendationId'}
                  component={widgetType}
                  settings={recommendedSettings}
                  data={data}
                  updateCallback={() => {
                    return null;
                  }}
                  isInEditMode={false}
                  model={{} as WidgetModel<{}>}
                />
              </CardRecommendationBody>
              <CardText>
                <SelectButton
                  color="success"
                  block
                  onClick={this.recommendationSelected.bind(
                    this,
                    recommendedSettings,
                  )}
                >
                  Select
                </SelectButton>
              </CardText>
            </CardRecommendation>
          );
        });
      });
    } else {
      return <h4>No recommendations</h4>;
    }
  }
}

export default CbChartRecommendation;



