import { createSelector } from 'reselect';
import cloneDeep from 'lodash/cloneDeep';
import { State } from './reducer';

import PortalModel from '../../cb-utils/models/PortalModel';
import { IChartRecommendation } from 'cb-utils/chartRecommendation/chartRecommendation';
import { AppState } from 'reducers';
import { PluginSettingDefinitionTypes } from 'utils/types';
import WidgetModel from 'cb-utils/models/widget/WidgetModel';

const portalModel = PortalModel.getInstance();

/**
 * Direct selector to the widgetSettings state domain
 */
const selectWidgetSettingsDomain = () => (state: AppState): State =>
  state.widgetSettings;

/**
 * Other specific selectors
 */

const selectWidgetInstance = () =>
  createSelector(selectMostRecentWidgetUpdate(), () => {
    // todo: figure out if there's a way to where we don't need to return a default 'mock' object
    return (
      cloneDeep(portalModel.getTemporaryWidget()) || {
        id: '',
        definition: {
          type_name: '',
        },
        externalScripts: [],
        updateCallback: () => {
          return;
        },
        settings: () => {
          return;
        },
        latestData: () => {
          return;
        },
        getSettingsForDefinition: (): PluginSettingDefinitionTypes[] => {
          return [];
        },
        getPreferredIncomingDatasources: () => {
          return;
        },
        getPreferredOutgoingDatasources: () => {
          return;
        },
      }
    );
  });

const selectWidgetInfo = () =>
  createSelector(selectWidgetInstance(), (widgetInstance) => {
    return {
      id: widgetInstance.id,
      type: widgetInstance.definition.type_name,
      props: widgetInstance.settings(),
      externalScripts: widgetInstance.externalScripts,
    };
  });

const selectWidgetProps = () =>
  createSelector(selectWidgetInfo(), (substate) => {
    return substate.props;
  });

const selectShowRecommendationPanel = () =>
  createSelector(selectWidgetSettingsDomain(), (substate) => {
    return substate.showRecommendationPanel;
  });

const selectCurrentParserType = () =>
  createSelector(selectWidgetSettingsDomain(), (substate) => {
    return substate.currentParserType;
  });

const selectCurrentParserScript = () =>
  createSelector(selectWidgetSettingsDomain(), (substate) => {
    return substate.currentParserScript;
  });

const selectCurrentParserSourceType = () =>
  createSelector(selectWidgetSettingsDomain(), (substate) => {
    return substate.currentParserSourceType;
  });

const selectCurrentParserSetting = () =>
  createSelector(selectWidgetSettingsDomain(), (substate) => {
    return substate.currentParserSetting;
  });

const selectShowEditParser = () =>
  createSelector(selectWidgetSettingsDomain(), (substate) => {
    return substate.showEditParser;
  });

const selectShowRecommendationBadge = () =>
  createSelector(selectWidgetSettingsDomain(), (substate) => {
    return substate.showRecommendationBadge;
  });

export const getNumberOfRecommendations = (recs: IChartRecommendation[]) => {
  return recs.reduce((recAccumulator, rec) => {
    return (
      recAccumulator +
      rec.possibilities.reduce((possAccumulator) => possAccumulator + 1, 0)
    );
  }, 0);
};

const selectNumberOfRecommendations = () =>
  createSelector(selectWidgetSettingsDomain(), (substate) => {
    const recs = substate.recommendations;
    return getNumberOfRecommendations(recs);
  });

const selectIsLoaded = () =>
  createSelector(selectWidgetSettingsDomain(), (substate) => {
    return substate.isLoaded;
  });

const selectSettingsForWidgetType = () =>
  createSelector(selectWidgetInstance(), (substate) => {
    return substate.getSettingsForDefinition();
  });

const selectPreferredIncomingDatasources = () =>
  createSelector(selectSettingsForWidgetType(), (settings) => {
    if (settings) {
      return portalModel.getIncomingDatasourcesForWidget(settings);
    }
  });

const selectPreferredOutgoingDatasources = () =>
  createSelector(selectSettingsForWidgetType(), (settings) => {
    if (settings) {
      return portalModel.getOutgoingDatasourcesForWidget(settings);
    }
  });

const selectWidgetTypes = () =>
  createSelector(selectIsLoaded(), () => {
    return portalModel.getWidgetTypes();
  });

const selectDatasourceInstances = () =>
  createSelector(selectIsLoaded(), () => {
    return portalModel.getDatasourceInstances();
  });

const selectMostRecentWidgetUpdate = () =>
  createSelector(selectWidgetSettingsDomain(), (substate) => {
    return substate.mostRecentWidgetUpdate;
  });

const selectWidgetData = () =>
  createSelector(selectWidgetInstance(), (widgetInstance) => {
    return widgetInstance.latestData();
  });

const selectUpdateCallbackForWidget = () =>
  createSelector(
    selectWidgetInstance(),
    (widgetInstance): WidgetModel<{}>['updateCallback'] => {
      return widgetInstance.updateCallback
        ? widgetInstance.updateCallback.bind(widgetInstance)
        : () => {
            return;
          };
    },
  );

const selectRecommendations = () =>
  createSelector(selectWidgetSettingsDomain(), (substate) => {
    return substate.recommendations;
  });

const selectShowExternalResources = () =>
  createSelector(selectWidgetSettingsDomain(), (substate) => {
    return substate.showExternalResources;
  });

const selectExternalResources = () =>
  createSelector(selectWidgetSettingsDomain(), (substate) => {
    return substate.externalResources;
  });

export {
  selectWidgetSettingsDomain,
  selectWidgetInfo,
  selectShowRecommendationPanel,
  selectCurrentParserType,
  selectCurrentParserScript,
  selectCurrentParserSourceType,
  selectCurrentParserSetting,
  selectShowEditParser,
  selectShowRecommendationBadge,
  selectNumberOfRecommendations,
  selectIsLoaded,
  selectSettingsForWidgetType,
  selectWidgetTypes,
  selectDatasourceInstances,
  selectWidgetData,
  selectRecommendations,
  selectWidgetProps,
  selectUpdateCallbackForWidget,
  selectShowExternalResources,
  selectExternalResources,
  selectPreferredIncomingDatasources,
  selectPreferredOutgoingDatasources,
};



