/**
 *
 * CbStepZilla
 *
 */

import React from 'react';
// import styled from 'styled-components';

import { Step, Stepper, StepButton } from 'material-ui/Stepper';
import StepConnector from './StepConnector';

interface IStep {
  name: string;
  component: JSX.Element;
}

interface IProps {
  steps: IStep[];
}

interface IState {
  currentStep: number;
}

class CbStepZilla extends React.Component<IProps, IState> {
  state = {
    currentStep: 0,
  };

  componentWillReceiveProps(nextProps: IProps) {
    if (
      nextProps.steps.length !== this.props.steps.length ||
      !this.stepsAreIdentical(nextProps.steps, this.props.steps)
    ) {
      this.setState({
        currentStep: 0,
      });
    }
  }

  render() {
    return (
      <div>
        <Stepper
          style={{ placeContent: 'center', flexFlow: 'row wrap' }}
          // @ts-ignore
          connector={StepConnector()}
        >
          {this.props.steps.map(this._renderStep)}
        </Stepper>
        {this.props.steps[this.state.currentStep].component}
      </div>
    );
  }

  stepsAreIdentical(next: IStep[], cur: IStep[]) {
    for (let i = 0, nextLen = next.length; i < nextLen; i += 1) {
      if (next[i].name !== cur[i].name) {
        return false;
      }
    }
    return true;
  }

  _renderStep = (step: IStep, i: number) => {
    return (
      <Step
        key={i}
        active={this.state.currentStep === i}
        data-test="widgetSettingCategory"
      >
        <StepButton onClick={this._goToStep.bind(this, i)}>
          {step.name}
        </StepButton>
      </Step>
    );
  };

  _goToStep = (idx: number) => {
    this.setState({
      currentStep: idx,
    });
  };
}

export default CbStepZilla;



