/*
 *
 * WidgetSettings reducer
 *
 */

import produce from 'immer';
import { ActionConstants } from './actionConstants';
import ActionTypes from './actions';

import { IChartRecommendation } from 'cb-utils/chartRecommendation/chartRecommendation';
import { PARSER_TYPES } from '../Portal/parserTypes';
import { ParserInstanceTypes } from 'utils/types';
import { IExternalResource } from 'cb-utils/externalResourceUtils';

export interface State {
  isLoaded: boolean;
  mostRecentWidgetUpdate: {
    time: number;
    type: string;
  };
  showEditParser: boolean;
  showRecommendationPanel: boolean;
  showRecommendationBadge: boolean;
  recommendations: IChartRecommendation[];
  currentParserSetting: string;
  currentParserSourceType: PARSER_TYPES | null;
  currentParserType: ParserInstanceTypes | null;
  currentParserScript: string;
  showExternalResources: boolean;
  externalResources: IExternalResource[];
}

export const initialState: State = {
  isLoaded: false,
  showEditParser: false,
  showRecommendationPanel: false,
  showRecommendationBadge: false,
  recommendations: [],
  mostRecentWidgetUpdate: {
    time: 0,
    type: '',
  },
  currentParserSetting: '',
  currentParserSourceType: null,
  currentParserType: null,
  currentParserScript: '',
  externalResources: [],
  showExternalResources: false,
};

const widgetSettingsReducer = produce(
  (state: State = initialState, action: ActionTypes) => {
    switch (action.type) {
      case ActionConstants.INIT_WIDGET_SETTINGS_SUCCESS:
        state.isLoaded = true;
        state.mostRecentWidgetUpdate = {
          type: ActionConstants.INIT_WIDGET_SETTINGS_SUCCESS,
          time: Date.now(),
        };
        state.externalResources = action.payload.externalResources;
        return;
      case ActionConstants.TOGGLE_RECOMMENDATION_PANEL:
        state.showRecommendationPanel = !state.showRecommendationPanel;
        state.showRecommendationBadge = false;
        return;
      case ActionConstants.DISMISS_PARSER_EDITOR:
        state.showEditParser = false;
        return;
      case ActionConstants.RECOMMENDATION_SELECTED:
        state.mostRecentWidgetUpdate = {
          time: Date.now(),
          type: ActionConstants.RECOMMENDATION_SELECTED,
        };
        state.showRecommendationPanel = false;
        state.showRecommendationBadge = false;
        return;
      case ActionConstants.ON_OPEN_PARSER:
        // in order to update the proper state when we close the parser modal, we need to keep track of the following -
        // which setting we are editing
        // which datasource we are editing
        // the script to edit (in order to pass it into the modal)
        // the type of source (incoming vs. outgoing)
        state.showEditParser = true;
        state.currentParserSetting = action.payload.settingName;
        state.currentParserSourceType = action.payload.sourceType;
        state.currentParserType = action.payload.parserType;
        state.currentParserScript = action.payload.script;
        return;
      case ActionConstants.ON_WIDGET_TYPE_CHANGE:
        state.mostRecentWidgetUpdate = {
          time: Date.now(),
          type: ActionConstants.ON_WIDGET_TYPE_CHANGE,
        };
        state.showRecommendationBadge = false;
        state.recommendations = [];
        return;
      case ActionConstants.ON_CLOSE_PARSER_EDITOR:
        state.showEditParser = false;
        return;
      case ActionConstants.UPDATE_RECOMMENDATIONS:
        state.recommendations = action.payload;
        state.showRecommendationBadge = true;
        return;
      case ActionConstants.WIDGET_UPDATED:
        state.mostRecentWidgetUpdate = {
          time: action.payload.time,
          type: action.payload.type,
        };
        return;
      case ActionConstants.OPEN_EXTERNAL_RESOURCES_MODAL:
        state.showExternalResources = true;
        return;
      case ActionConstants.DISMISS_EXTERNAL_RESOURCES_MODAL:
        state.showExternalResources = false;
        return;
      case ActionConstants.UPDATE_EXTERNAL_RESOURCES:
        state.showExternalResources = false;
        state.externalResources = action.payload.externalResources;
        return;
      case ActionConstants.CLEAN_UP_TEMP_WIDGET:
        state.isLoaded = false;
        return;
      default:
        return state;
    }
  },
);

export default widgetSettingsReducer;



