/**
 *
 * WidgetSettingsModal
 *
 */

import React from 'react';
import styled from 'styled-components';

// import {FormattedMessage} from 'react-intl';
import messages from './messages';

import {
  ModalBody,
  ModalFooter,
  FormGroup,
  Input,
  Label,
  Col,
  Row,
  Button,
} from 'reactstrap';

import {
  INCOMING_PARSER,
  OUTGOING_PARSER,
  DATA_SETTING_TYPE,
} from '../../containers/Portal/constants';

import { NEW_WIDGET_ID } from '../../containers/Portal/pluginConstants';

import PortalModel from '../../cb-utils/models/PortalModel';

const portalModel = PortalModel.getInstance();

import DatasourceEditor from '../DatasourceEditor';

import CbStepZilla from '../CbStepZilla';
import EditParserModal from '../EditParserModal';
import groupOptions from '../../cb-utils/groupOptions';
import allRequiredSettingsForPluginHaveData from '../../cb-utils/allRequiredSettingsForPluginHaveData';
import CbWidget from '../CbWidget';
import CbChartRecommendation from '../CbChartRecommendation';
import CbBadgeButton from '../CbBadgeButton';
import CbBurgerMenu from '../CbBurgerMenu';
import FullSizeModal from '../FullSizeModal';
import ExternalScriptModal from '../ExternalScriptModal';

import MagicIcon from 'react-icons/lib/fa/magic';

import groupBy from 'lodash/groupBy';
import { PARSER_TYPES } from 'containers/Portal/parserTypes';
import {
  CBEvent,
  PluginSettingDefinitions,
  SettingDefinition,
  WidgetDefinition,
  IDatasourceSuggestions,
  DynamicDataSettingInstance,
  DataSettingDefinition,
  IParserInfo,
  PluginSettingDefinitionTypes,
  CbWidgetProps,
  WidgetUpdateCallback,
  ParserInstanceTypes,
  Omit,
} from 'utils/types';
import { Map, AnyMap } from 'cb-utils/console-entity-models';
import { IChartRecommendation } from 'cb-utils/chartRecommendation/chartRecommendation';
import { IExternalResource, IDiffInfo } from 'cb-utils/externalResourceUtils';
import DatasourceModel from 'cb-utils/models/datasource/DatasourceModel';
import { Formik, FormikProps, Field } from 'formik';
import FormikInputWrapper from 'components/FormikInputWrapper';
import FormikForm from 'components/FormikForm';
import CbButton from 'components/CbButton';
import { WidgetTypes } from 'plugins/widgets/types';
import ImportPluginButton from '../ImportPluginButton';
import { isWidgetFile } from './utils';
import { IWidgetInfo } from 'cb-utils/models/widget/WidgetModel';
import ExportPluginButton from '../ExportPluginButton';
import FlexModalHeader from '../FlexModalHeader';
import { injectIntl } from 'react-intl';
import { BaseHTMLModel } from 'plugins/widgets/HTMLWidget/def';
import { CodeFileIcon } from '../Icons';

const WidgetSettingsForm = styled.div`
  /*float left so that the form and the live display are side by side*/
  float: left;
  padding: 15px;
  width: 50%;
  height: 100%;
  display: inline-block;
  border-right: 1px solid #e5e5e5;
  overflow-y: auto;
`;

const WidgetLiveDisplay = styled.div`
  display: inline-block;
  padding: 15px;
  width: 50%;
  height: 100%;
  background-color: ${(props: { backgroundColor: string }) =>
    props.backgroundColor};
`;

const WidgetSettingsModalBody = styled(ModalBody)`
  && {
    padding: 0;
  }
`;

export interface IDataForParserError {
  settingName: string;
  dataType: PARSER_TYPES;
  parserType: string;
}

interface IWidgetSettingsModalProps {
  showParserForError: boolean;
  currentParserSetting: string;
  settingsForWidgetType: PluginSettingDefinitions;
  showRecommendationPanel: boolean;
  title: string;
  isNewWidget: boolean;
  parentPaneBackgroundColor: string;
  updateCallbackForWidget: WidgetUpdateCallback<{}>;
  showExternalResources: boolean;
  showEditParser: boolean;
  currentParserScript: string;
  currentParserSourceType: PARSER_TYPES;
  currentParserType: ParserInstanceTypes;
  showRecommendationBadge: boolean;
  numberOfRecommendations: number;
  openExternalResourcesModal: () => void;
  toggleRecommendationPanel: () => void;
  dismissParserEditor: () => void;
  onCalculatedContentChange: (settingName: string, e: string) => void;
  onDatasourceChange: (settingName: string, val: string, data: AnyMap) => void;
  onDataTypeChange: (settingName: string, e: string) => void;
  onDebugToggle: (settingName: string, parserType: string) => void;
  onOpenParser: (
    settingName: string,
    sourceType: PARSER_TYPES,
    parserType: string,
  ) => void;
  onWidgetSettingChange: (
    settingName: string,
    setting: SettingDefinition,
    val: any,
    data: AnyMap,
  ) => void;
  onWidgetTypeChange: (type: WidgetTypes) => void;
  dismissModal: () => void;
  paneId: string;
  tabId: string;
  target: string;
  pageId: string;
  closeModal: (rtn: IWidgetSettingsReturn) => void;
  widgetInfo: IWidgetInfo<AnyMap>;
  dataForError: {
    settingName: string;
    dataType: PARSER_TYPES;
    parserType: string;
  };
  widgetData: AnyMap;
  recommendations: IChartRecommendation[];
  widgetTypes: Map<WidgetDefinition>;
  currentExternalResources: IExternalResource[];
  dismissExternalResourcesModal: () => void;
  datasourceInstances: Map<DatasourceModel>;
  incomingDatasources: IDatasourceSuggestions;
  outgoingDatasources: IDatasourceSuggestions;
  recommendationSelected: (newSettings: AnyMap) => void;
  closeParserEditor: (code: string) => void;
  testParser: (code: string) => void;
  updateExternalResources: (
    info: { scripts: IExternalResource[]; diff: IDiffInfo },
  ) => void;
  importWidget: (w: IWidgetInfo<{}>) => void;
}

export interface WidgetSettingsInfo
  extends Omit<IWidgetInfo<{}>, 'externalScripts'> {
  externalScripts: IExternalResource[];
}

export interface IWidgetSettingsReturn {
  pageId: string;
  paneId: string;
  tabId: string;
  target: string;
  widgetInfo: WidgetSettingsInfo;
}

type WidgetInfoProps = Map<DynamicDataSettingInstance>;

class WidgetSettingsModal extends React.PureComponent<
  IWidgetSettingsModalProps & ReactIntl.InjectedIntlProps,
  {}
> {
  dataTypes = [
    {
      value: PARSER_TYPES.STATIC,
      label: 'Static',
    },
    {
      value: PARSER_TYPES.CALCULATED,
      label: 'Calculated',
    },
    {
      value: PARSER_TYPES.DYNAMIC,
      label: 'Dynamic',
    },
  ];

  componentDidMount() {
    if (this.props.showParserForError) {
      const data = this.props.dataForError;
      this.onOpenParser(data.settingName, data.dataType, data.parserType);
    }
  }

  render() {
    let theExpectedFormatTips = '';
    if (!!this.props.currentParserSetting) {
      for (const setting of this.props.settingsForWidgetType) {
        if (
          setting.name === this.props.currentParserSetting &&
          !!(setting as DataSettingDefinition).helpObj &&
          !!(setting as DataSettingDefinition).helpObj.expectedFormatTips
        ) {
          theExpectedFormatTips = (setting as DataSettingDefinition).helpObj
            .expectedFormatTips;
          break;
        }
      }
    }
    const { widgetInfo } = this.props;
    return (
      <Formik
        validationSchema={this.getValidationForWidget()}
        initialValues={widgetInfo.props}
        onSubmit={(values: WidgetInfoProps) => {
          this._onSubmit();
        }}
        isInitialValid={this.getValidationForWidget() === undefined}
        enableReinitialize={true}
        render={(fprops: FormikProps<WidgetInfoProps>) => (
          <FullSizeModal
            isOpen={true}
            onHide={this._dismissModal}
            keyboard={false}
          >
            <FlexModalHeader>
              {this.props.title}
              <div className="ml-auto">
                {this.props.isNewWidget ? (
                  <ImportPluginButton
                    btnText={messages.importWidget}
                    shapeChecker={isWidgetFile}
                    onComplete={this.props.importWidget}
                    successMsg={this.props.intl.formatMessage(
                      messages.importWidgetSuccess,
                    )}
                    badShapeMsg={this.props.intl.formatMessage(
                      messages.importWidgetError,
                    )}
                    helpInfo={messages.importWidgetHelp}
                  />
                ) : (
                  <ExportPluginButton
                    btnText={messages.exportWidget}
                    plugin={widgetInfo}
                    fileName={widgetInfo.id}
                    helpInfo={messages.exportWidgetHelp}
                  />
                )}
              </div>
            </FlexModalHeader>

            <WidgetSettingsModalBody>
              <CbBurgerMenu
                isOpen={this.props.showRecommendationPanel}
                onChange={this._panelStateChange}
                width="50vw"
              >
                {this.props.showRecommendationPanel ? (
                  <CbChartRecommendation
                    type={portalModel.getWidgetClassByType(widgetInfo.type)}
                    settings={widgetInfo.props}
                    data={this.props.widgetData}
                    recommendations={this.props.recommendations}
                    widgetDefinition={this.props.settingsForWidgetType}
                    recommendationSelected={this._recommendationSelected}
                  />
                ) : null}
              </CbBurgerMenu>

              <WidgetSettingsForm>
                <FormikForm {...fprops}>
                  <FormGroup row>
                    <Col sm={3}>
                      <Label>Type</Label>
                    </Col>
                    <Col sm={9}>
                      <Input
                        type="select"
                        placeholder="Select type..."
                        value={widgetInfo.type}
                        onChange={this._onWidgetTypeChange}
                        data-test="selectWidgetType"
                      >
                        {this.renderWidgetTypes(this.props.widgetTypes)}
                      </Input>
                    </Col>
                  </FormGroup>
                  {this._renderCustomProps(this.props.settingsForWidgetType)}
                </FormikForm>
              </WidgetSettingsForm>

              <WidgetLiveDisplay
                backgroundColor={this.props.parentPaneBackgroundColor}
                data-cy="widgetPreview"
              >
                <CbWidget
                  id={widgetInfo.id || NEW_WIDGET_ID}
                  model={portalModel.getTemporaryWidget()}
                  component={
                    portalModel.getWidgetClassByType(
                      widgetInfo.type,
                    ) as React.ComponentClass<CbWidgetProps<AnyMap, AnyMap>>
                  }
                  settings={widgetInfo.props}
                  data={this.props.widgetData}
                  updateCallback={this.props.updateCallbackForWidget}
                  isInEditMode={true}
                />
              </WidgetLiveDisplay>

              {this.props.showExternalResources && (
                <ExternalScriptModal
                  onClose={this.props.updateExternalResources}
                  onDismiss={this.props.dismissExternalResourcesModal}
                  scripts={this.props.currentExternalResources}
                />
              )}

              {this.props.showEditParser && (
                <EditParserModal
                  onDismiss={this._dismissParserEditor}
                  onClose={this._closeParserEditor}
                  parser={this.props.currentParserScript}
                  onTest={this._testParser}
                  split={
                    !!(this.props
                      .settingsForWidgetType[0] as DataSettingDefinition).split
                  }
                  currentParserSourceType={this.props.currentParserSourceType}
                  currentParserType={this.props.currentParserType}
                  expectedFormatTips={theExpectedFormatTips}
                />
              )}
            </WidgetSettingsModalBody>

            <ModalFooter>
              <div className="mr-auto">
                <CbBadgeButton
                  shouldShowBadge={this.props.showRecommendationBadge}
                  count={this.props.numberOfRecommendations}
                >
                  <Button onClick={this._toggleRecommendationPanel}>
                    <MagicIcon />
                  </Button>
                </CbBadgeButton>

                <Button
                  style={{ marginLeft: '.5rem' }}
                  onClick={this.props.openExternalResourcesModal}
                >
                  <CodeFileIcon />
                </Button>
              </div>

              {!this.props.showEditParser && (
                <Button color="default" onClick={this._dismissModal}>
                  Cancel
                </Button>
              )}
              {!this.props.showEditParser && (
                <CbButton
                  color="primary"
                  onClick={fprops.handleSubmit}
                  errors={fprops.errors}
                  touched={fprops.touched}
                >
                  Apply
                </CbButton>
              )}
            </ModalFooter>
          </FullSizeModal>
        )}
      />
    );
  }

  _toggleRecommendationPanel = () => {
    this.props.toggleRecommendationPanel();
  };

  _panelStateChange = ({ isOpen }: { isOpen: boolean }) => {
    // this function is called by the burger menu. we only want to toggle if the state is new
    // i.e., the user clicks the X button or they click away from the menu
    if (isOpen !== this.props.showRecommendationPanel) {
      this.props.toggleRecommendationPanel();
    }
  };

  _testParser = (val: string) => {
    this.props.testParser(val);
  };

  _closeParserEditor = (val: string) => {
    this.props.closeParserEditor(val);
  };

  _dismissParserEditor = () => {
    this.props.dismissParserEditor();
  };

  _recommendationSelected = (newSettings: AnyMap) => {
    this.props.recommendationSelected(newSettings);
  };

  onCalculatedContentChange(settingName: string, e: CBEvent) {
    this.props.onCalculatedContentChange(settingName, e.target.value);
  }

  onDatasourceChange(settingName: string, e: CBEvent) {
    this.props.onDatasourceChange(
      settingName,
      e.target.value,
      this.props.widgetData,
    );
  }

  onDataTypeChange(settingName: string, e: CBEvent) {
    this.props.onDataTypeChange(settingName, e.target.value);
  }

  onDebugToggle(settingName: string, parserType: string) {
    this.props.onDebugToggle(settingName, parserType);
  }

  onOpenParser(
    settingName: string,
    sourceType: PARSER_TYPES,
    parserType: string,
  ) {
    this.props.onOpenParser(settingName, sourceType, parserType);
  }

  onWidgetSettingChange(
    settingName: string,
    setting: SettingDefinition,
    e: CBEvent,
  ) {
    this.props.onWidgetSettingChange(
      settingName,
      setting,
      e.target.value,
      this.props.widgetData,
    );
  }

  _onWidgetTypeChange = (e: CBEvent) => {
    this.props.onWidgetTypeChange(e.target.value);
  };

  _dismissModal = () => {
    this.props.dismissModal();
  };

  _onSubmit = () => {
    const { widgetInfo } = this.props;
    const rtn = {
      paneId: this.props.paneId,
      tabId: this.props.tabId,
      target: this.props.target,
      pageId: this.props.pageId,
      widgetInfo: {
        type: widgetInfo.type,
        props: widgetInfo.props,
        id: widgetInfo.id,
        externalScripts: this.props.currentExternalResources,
        tab: '',
      },
    };
    this.props.closeModal(rtn);
  };

  getTypeForDatasourceSetting(settingName: string) {
    if (this.props.widgetInfo.props[settingName]) {
      return this.props.widgetInfo.props[settingName].dataType;
    }
  }

  getValueForDatasourceSetting(settingName: string) {
    return this.props.widgetInfo.props[settingName].value as
      | BaseHTMLModel
      | string;
  }

  getIdForDatasourceSetting(settingName: string) {
    // note: this can throw an error when switching to an external plugin that uses external_scripts
    // todo: consider delaying stuff when loading an external plugin
    return this.props.widgetInfo.props[settingName].id;
  }

  hasParserType(settingName: string, parserType: string) {
    return this.props.widgetInfo.props[settingName][parserType] ? true : false;
  }

  availableDataTypes(settingName: string) {
    if (
      typeof this.props.widgetInfo.props[settingName] === 'object' &&
      this.props.widgetInfo.props[settingName].force_data
    ) {
      return this.dataTypes.filter((dataType) =>
        this.props.widgetInfo.props[settingName].force_data.includes(
          dataType.value,
        ),
      );
    } else {
      return this.dataTypes;
    }
  }

  getToggleStatusForParser(settingName: string, parserType: string) {
    if (this.props.widgetInfo.props[settingName][parserType]) {
      return (this.props.widgetInfo.props[settingName][
        parserType
      ] as IParserInfo).isDebugOn;
    } else {
      return false;
    }
  }

  getValueForSetting(settingName: string) {
    return this.props.widgetInfo.props[settingName];
  }

  getValidationForWidget() {
    return this.props.widgetTypes[this.props.widgetInfo.type].validationSchema;
  }

  _getWidgetTypeForSetting = (
    setting: PluginSettingDefinitionTypes,
    i: number,
  ) => {
    switch (setting.type) {
      case DATA_SETTING_TYPE:
        return (
          <DatasourceEditor
            data-cy="settingField"
            key={i}
            settingModel={setting}
            currentType={this.getTypeForDatasourceSetting(setting.name)}
            currentDatasourceId={this.getIdForDatasourceSetting(setting.name)}
            currentValue={this.getValueForDatasourceSetting(setting.name)}
            hasIncomingParser={this.hasParserType(
              setting.name,
              INCOMING_PARSER,
            )}
            hasOutgoingParser={this.hasParserType(
              setting.name,
              OUTGOING_PARSER,
            )}
            isIncomingParserDebugOn={this.getToggleStatusForParser(
              setting.name,
              INCOMING_PARSER,
            )}
            isOutgoingParserDebugOn={this.getToggleStatusForParser(
              setting.name,
              OUTGOING_PARSER,
            )}
            dataTypes={this.availableDataTypes(setting.name)}
            datasourceInstances={this.props.datasourceInstances}
            incomingDatasources={this.props.incomingDatasources}
            outgoingDatasources={this.props.outgoingDatasources}
            onDataTypeChange={this.onDataTypeChange.bind(this, setting.name)}
            onDatasourceChange={this.onDatasourceChange.bind(
              this,
              setting.name,
            )}
            onCalculatedContentChange={this.onCalculatedContentChange.bind(
              this,
              setting.name,
            )}
            onDebugToggle={this.onDebugToggle.bind(this, setting.name)}
            onOpenParser={this.onOpenParser.bind(this, setting.name)}
            dynamicTypeConstant={PARSER_TYPES.DYNAMIC}
            calculatedTypeConstant={PARSER_TYPES.CALCULATED}
            staticTypeConstant={PARSER_TYPES.STATIC}
          />
        );
      default:
        return (
          <Field
            key={setting.name + i}
            data-cy="settingField"
            {...setting}
            name={setting.name}
            label={setting.display_name}
            asRow={false}
            onChange={this.onWidgetSettingChange.bind(
              this,
              setting.name,
              setting,
            )}
            size={6}
            type={setting.type}
            component={FormikInputWrapper}
            latestData={this.props.widgetData}
          />
        );
    }
  };

  _createSettingsList = (settings: PluginSettingDefinitions) => {
    if (settings) {
      return (
        <Row data-cy="settingsRow">
          {settings.map(this._getWidgetTypeForSetting.bind(this))}
        </Row>
      );
    }
  };

  createGroupedSettings(settings: PluginSettingDefinitions) {
    const groups = groupBy(
      settings,
      (setting) => (setting.group ? setting.group : 'Other'),
    );
    const steps = [];
    let currentStep;
    for (const group in groups) {
      if (groups.hasOwnProperty(group)) {
        currentStep = {
          name: group,
          component: <div>{this._createSettingsList(groups[group])}</div>,
        };
        steps.push(currentStep);
      }
    }
    return <CbStepZilla steps={steps} />;
  }

  _renderCustomProps = (settings: PluginSettingDefinitions) => {
    if (settings && settings[0] && settings[0].group) {
      return this.createGroupedSettings(settings);
    } else {
      return this._createSettingsList(settings);
    }
  };

  renderWidgetTypes(types: Map<WidgetDefinition>) {
    return groupOptions(types);
  }

  _allRequiredSettingsHaveData = () => {
    return allRequiredSettingsForPluginHaveData(
      this.props.settingsForWidgetType,
      this.props.widgetInfo.props,
    );
  };
}

export default injectIntl(WidgetSettingsModal);



