/**
 *
 * ParserButton
 *
 */

import React from 'react';

import { ButtonGroup, Button } from 'reactstrap';
import EditIcon from 'react-icons/lib/fa/edit';
import EyeSlashIcon from 'react-icons/lib/fa/eye-slash';
import EyeIcon from 'react-icons/lib/fa/eye';

interface IProps {
  onOpenParser: () => void;
  title: string;
  isDebugOn: boolean;
  onDebugToggle: () => void;
}

class ParserButton extends React.PureComponent<IProps> {
  render() {
    return (
      <ButtonGroup>
        <Button color="primary" onClick={this.props.onOpenParser}>
          <EditIcon />
          &nbsp;
          {this.props.title}
        </Button>

        <Button
          outline
          color="primary"
          active={this.props.isDebugOn}
          onClick={this.props.onDebugToggle}
        >
          Debug &nbsp;
          {this.props.isDebugOn ? <EyeIcon /> : <EyeSlashIcon />}
        </Button>
      </ButtonGroup>
    );
  }
}

export default ParserButton;



