/**
 *
 * CollapsibleAreaTitle
 *
 */

import React, { ComponentClass } from 'react';
// import styled from 'styled-components';

import CaretDown from 'react-icons/lib/fa/caret-down';
import CaretUp from 'react-icons/lib/fa/caret-up';

interface IProps {
  tag: ComponentClass<{ onClick: () => void }>;
  title: string;
  isOpen: boolean;
  onClick: () => void;
}

class CollapsibleAreaTitle extends React.PureComponent<IProps, {}> {
  render() {
    const { tag: WrapperTag, title, isOpen, onClick } = this.props;
    return (
      <WrapperTag onClick={onClick}>
        {title}
        <span style={{ float: 'right' }}>
          {isOpen ? <CaretUp /> : <CaretDown />}
        </span>
      </WrapperTag>
    );
  }
}

export default CollapsibleAreaTitle;



