/*
 * WidgetSettingsModal Messages
 *
 * This contains all the text for the WidgetSettingsModal component.
 */
import { defineMessages } from 'react-intl';

export default defineMessages({
  importWidget: {
    id: 'app.components.WidgetSettingsModal.importWidget',
    defaultMessage: 'Import widget',
  },
  importWidgetSuccess: {
    id: 'app.components.WidgetSettingsModal.importWidgetSuccess',
    defaultMessage: 'Successfully imported widget',
  },
  importWidgetError: {
    id: 'app.components.WidgetSettingsModal.importWidgetError',
    defaultMessage: 'Incorrect widget format',
  },
  exportWidget: {
    id: 'app.components.WidgetSettingsModal.exportWidget',
    defaultMessage: 'Export widget',
  },
  importWidgetHelp: {
    id: 'app.components.WidgetSettingsModal.importWidgetHelp',
    defaultMessage:
      'Widget configurations can be imported from any portal. To create a widget from a saved configuration, first create a widget with the desired settings and save it. Once the widget has been saved, open the edit dialog for the widget and hit export. This will trigger a download of the widgets .json file which can then be imported here. Note that any datasources or external resources that the widget is dependent upon will need to be imported into the portal separately.',
  },
  exportWidgetHelp: {
    id: 'app.components.WidgetSettingsModal.exportWidgetHelp',
    defaultMessage:
      'Export this widgets .json file in order to reuse this widgets configuration in a portal. Note that any necessary datasources or external resources that the widget is dependent upon will need to be exported separately.',
  },
});



