import {
  PluginSettingDefinitions,
  SettingTypes,
  MultiObjectSettingDefinition,
} from 'utils/types';
import { AnyMap } from './console-entity-models';

const allRequiredSettingsForPluginHaveData = (
  settings: PluginSettingDefinitions,
  values: AnyMap,
) => {
  if (settings) {
    for (const setting of settings) {
      const settingValue = values[setting.name];
      if (setting.type === SettingTypes.MULTI_OBJECT_SETTING_TYPE) {
        // If the setting is a multi-object-setting, the value of the setting will be an array
        // containing an object representing the values for each field within a group of fields.
        //
        // [
        //  {
        //    "dataKey":"uv",
        //    "fill":"",
        //    "yAxisId":"t",
        //    "legendType":"star",
        //    "stackId":"",
        //    "name":""
        //  }
        // ]

        // objectShape will contain an array of objects, each object containing the metadata for
        // a specific field
        const objectShape = (setting as MultiObjectSettingDefinition)
          .objectShape;
        // "objectShape":[
        //  {
        //    "name":"dataKey",
        //    "display_name":"Data Key",
        //    "type":"dataKey","required":true
        //  },{
        //    "name":"fill",
        //    "display_name":"Fill",
        //    "default_value":"",
        //    "type":"color",
        //    "required":true
        //  },{
        //    "name":"yAxisId",
        //    "display_name":"Y Axis ID",
        //    "type":"text",
        //    "required":true
        //  },{
        //    "name":"legendType",
        //    "display_name":"Legend Type",
        //    "type":"option",
        //    "default_value":"star",
        //    "options":[
        //      {
        //        "name":"line",
        //        "value":"line"
        //      },{
        //        "name":"square",
        //        "value":"square"
        //      },{
        //        "name":"rect",
        //        "value":"rect"
        //      },{
        //        "name":"circle",
        //        "value":"circle"
        //      },{
        //        "name":"cross",
        //        "value":"cross"
        //      },{
        //        "name":"diamond",
        //        "value":"diamond"
        //      },{
        //        "name":"star",
        //        "value":"star"
        //      },{
        //        "name":"triangle",
        //        "value":"triangle"
        //      },{
        //        "name":"wye",
        //        "value":"wye"
        //      }
        //    ],
        //    "required":true
        //  },{
        //    "name":"stackId",
        //    "display_name":"Stack ID",
        //    "type":"text",
        //    "required":true
        //  },{
        //    "name":"name",
        //    "display_name":"Name",
        //    "type":"text",
        //    "required":true
        //  }
        // ]
        for (const def of objectShape) {
          for (
            let j = 0, settingValueLen = settingValue.length;
            j < settingValueLen;
            j += 1
          ) {
            if (
              def.required &&
              (settingValue[j][def.name] === undefined ||
                settingValue[j][def.name] === '')
            ) {
              return false;
            }
          }
        }
      } else {
        if (
          !!setting.required &&
          (settingValue === undefined || settingValue === '')
        ) {
          return false;
        }
      }
    }
  }

  return true;
};

export default allRequiredSettingsForPluginHaveData;



