import { IWidgetInfo } from 'cb-utils/models/widget/WidgetModel';

export const isWidgetFile = (contents: IWidgetInfo<{}>) => {
  if (
    contents &&
    contents.id &&
    contents.type &&
    contents.props &&
    contents.externalScripts
  ) {
    return true;
  }
  return false;
};



