/**
 *
 * CbBadgeButton
 *
 */

import React from 'react';
import styled from 'styled-components';

import { Badge } from 'reactstrap';

const NotificationButton = styled.span`
  position: relative;
`;

const NotificationBadge = styled(Badge)`
  &&& {
    position: absolute;
    top: -10px;
    right: -8px;
  }
`;

interface IProps {
  shouldShowBadge: boolean;
  count: number;
  text?: string;
  className?: string;
}

class CbBadgeButton extends React.PureComponent<IProps, {}> {
  render() {
    return (
      <NotificationButton className={this.props.className}>
        {this.props.children}
        {this._renderBadge()}
      </NotificationButton>
    );
  }

  _renderBadge = () => {
    const textToRender =
      this.props.count && this.props.count > 0
        ? this.props.count
        : this.props.text;
    if (this.props.shouldShowBadge && textToRender) {
      return (
        <NotificationBadge color="danger">{textToRender}</NotificationBadge>
      );
    }
  };
}

export default CbBadgeButton;



