/**
 *
 * ExportPluginButton
 *
 */

import React from 'react';
import { DownloadIcon } from '../Icons';
import { FormattedMessage } from 'react-intl';
import ImportExportHelp from '../ImportExportHelp';
// import styled from 'styled-components';

interface ExportPluginButtonProps {
  btnText: FormattedMessage.MessageDescriptor;
  plugin: object;
  fileName: string;
  helpInfo?: FormattedMessage.MessageDescriptor;
}

class ExportPluginButton extends React.PureComponent<
  ExportPluginButtonProps,
  {}
> {
  render() {
    const { btnText, plugin, fileName, helpInfo } = this.props;
    return (
      <div>
        {helpInfo && <ImportExportHelp bodyText={helpInfo} />}
        <a
          data-cy="export-plugin-button"
          className="btn btn-primary"
          href={`data:text/json;charset=utf-8,${encodeURIComponent(
            JSON.stringify(plugin),
          )}`}
          download={`${fileName}.json`}
        >
          <DownloadIcon />
          &nbsp;
          <FormattedMessage {...btnText} />
        </a>
      </div>
    );
  }
}

export default ExportPluginButton;



