/*
 *
 * WidgetSettings
 *
 */

import React from 'react';
import { connect } from 'react-redux';
import { compose } from 'redux';

import injectReducer from '../../utils/injectReducer';
import injectSaga from '../../utils/injectSaga';

import reducer from './reducer';
import saga from './sagas';
import {
  selectWidgetInfo,
  selectShowRecommendationPanel,
  selectCurrentParserSetting,
  selectCurrentParserSourceType,
  selectCurrentParserScript,
  selectCurrentParserType,
  selectShowEditParser,
  selectShowRecommendationBadge,
  selectNumberOfRecommendations,
  selectIsLoaded,
  selectSettingsForWidgetType,
  selectDatasourceInstances,
  selectWidgetTypes,
  selectWidgetData,
  selectRecommendations,
  selectUpdateCallbackForWidget,
  selectShowExternalResources,
  selectExternalResources,
  selectPreferredIncomingDatasources,
  selectPreferredOutgoingDatasources,
} from './selectors';
import {
  cleanUpTempWidget,
  initWidgetSettingsRequest,
  toggleRecommendationPanel,
  dismissParserEditor,
  calculatedContentChange,
  datasourceChange,
  onDebugToggle,
  onOpenParserRequest,
  onWidgetSettingChangeRequest,
  testParserChanges,
  onDataTypeChangeRequest,
  recommendationSelectedRequest,
  onWidgetTypeChangeRequest,
  onCloseParserEditorRequest,
  openExternalResources,
  dismissExternalResourcesModal,
  updateExternalResources,
  InitWidgetSettingsModalPayload,
  importWidgetRequest,
} from './actions';

import WidgetSettingsModal, {
  IDataForParserError,
  IWidgetSettingsReturn,
} from '../../components/WidgetSettingsModal';
import LoadingIndicator from '../../components/LoadingIndicator';
import { CBDispatch } from 'cb-utils/typeUtils';
import { IWidgetInfo } from 'cb-utils/models/widget/WidgetModel';
import { AnyMap, Map } from 'cb-utils/console-entity-models';
import {
  ParserInstanceTypes,
  PluginSettingDefinitionTypes,
  WidgetDefinition,
} from 'utils/types';
import { PARSER_TYPES } from '../Portal/parserTypes';
import { WidgetTypes } from 'plugins/widgets/types';
import DatasourceModel from 'cb-utils/models/datasource/DatasourceModel';
import { createStructuredSelector } from 'reselect';
import { ICloseModalInfo } from 'components/ExternalScriptManager/ExternalScriptManagerContainer';

interface IProps extends InitWidgetSettingsModalPayload {
  onDismiss: () => void;
  onClose: (info: IWidgetSettingsReturn) => void;
  showParserError: boolean;
  widgetThemes: AnyMap;
  showParserForError: boolean;
  title: string;
  parentPaneBackgroundColor: string;
  paneId: string;
  tabId: string;
  target: string;
  pageId: string;
  dataForError: IDataForParserError;
  widgetTypes: Map<WidgetDefinition>;
  datasourceInstances: Map<DatasourceModel>;
}

export class WidgetSettings extends React.PureComponent<
  IProps & StateToProps & DispatchToProps
> {
  componentWillMount() {
    this.props.init();
  }

  render() {
    if (this.props.isLoaded) {
      return <WidgetSettingsModal {...this.props} />;
    } else {
      return <LoadingIndicator />;
    }
  }
}

type StateToProps = ReturnType<typeof mapStateToProps>;

const mapStateToProps = createStructuredSelector({
  isLoaded: selectIsLoaded(),
  widgetInfo: selectWidgetInfo(),
  widgetData: selectWidgetData(),
  settingsForWidgetType: selectSettingsForWidgetType(),
  updateCallbackForWidget: selectUpdateCallbackForWidget(),
  widgetTypes: selectWidgetTypes(),
  incomingDatasources: selectPreferredIncomingDatasources(),
  outgoingDatasources: selectPreferredOutgoingDatasources(),
  datasourceInstances: selectDatasourceInstances(),
  showRecommendationPanel: selectShowRecommendationPanel(),
  currentParserType: selectCurrentParserType(),
  currentParserScript: selectCurrentParserScript(),
  currentParserSetting: selectCurrentParserSetting(),
  currentParserSourceType: selectCurrentParserSourceType(),
  showEditParser: selectShowEditParser(),
  showRecommendationBadge: selectShowRecommendationBadge(),
  numberOfRecommendations: selectNumberOfRecommendations(),
  recommendations: selectRecommendations(),
  showExternalResources: selectShowExternalResources(),
  currentExternalResources: selectExternalResources(),
});

type DispatchToProps = ReturnType<typeof mapDispatchToProps>;

function mapDispatchToProps(dispatch: CBDispatch, props: IProps) {
  return {
    init: () => {
      dispatch(initWidgetSettingsRequest(props));
    },
    dismissModal: () => {
      dispatch(cleanUpTempWidget());
      props.onDismiss();
    },
    closeModal: (info: IWidgetSettingsReturn) => {
      dispatch(cleanUpTempWidget());
      props.onClose(info);
    },
    toggleRecommendationPanel: () => {
      dispatch(toggleRecommendationPanel());
    },
    dismissParserEditor: () => {
      dispatch(dismissParserEditor());
    },
    recommendationSelected: (newSettings: AnyMap) => {
      dispatch(recommendationSelectedRequest(newSettings));
    },
    onCalculatedContentChange: (settingName: string, val: string) => {
      dispatch(calculatedContentChange(settingName, val));
    },
    onDatasourceChange: (settingName: string, val: string, data: AnyMap) => {
      dispatch(datasourceChange(settingName, val, data));
    },
    onDebugToggle: (settingName: string, parserType: ParserInstanceTypes) => {
      dispatch(onDebugToggle(settingName, parserType));
    },
    onOpenParser: (
      settingName: string,
      sourceType: PARSER_TYPES,
      parserType: ParserInstanceTypes,
    ) => {
      dispatch(onOpenParserRequest(settingName, sourceType, parserType));
    },
    onWidgetSettingChange: (
      settingName: string,
      setting: PluginSettingDefinitionTypes,
      value: any,
      data: AnyMap,
    ) => {
      dispatch(
        onWidgetSettingChangeRequest(
          settingName,
          setting,
          value,
          props.widgetThemes,
          data,
        ),
      );
    },
    onWidgetTypeChange: (value: WidgetTypes) => {
      dispatch(onWidgetTypeChangeRequest(value, props.widgetThemes));
    },
    closeParserEditor: (value: string) => {
      dispatch(onCloseParserEditorRequest(value));
    },
    onDataTypeChange: (settingName: string, value: PARSER_TYPES) => {
      dispatch(onDataTypeChangeRequest(settingName, value));
    },
    testParser: (value: string) => {
      dispatch(testParserChanges(value));
    },
    openExternalResourcesModal: () => {
      dispatch(openExternalResources());
    },
    dismissExternalResourcesModal: () => {
      dispatch(dismissExternalResourcesModal());
    },
    updateExternalResources: (data: ICloseModalInfo) => {
      dispatch(updateExternalResources(data.scripts));
    },
    importWidget: (w: IWidgetInfo<{}>) => {
      dispatch(importWidgetRequest(w, props.widgetThemes));
    },
  };
}

const withConnect = connect(mapStateToProps, mapDispatchToProps);

const withReducer = injectReducer({ key: 'widgetSettings', reducer });
const withSaga = injectSaga({ key: 'widgetSettings', saga });

export default compose(withReducer, withSaga, withConnect)(WidgetSettings);



